<template>
  <div class="auth_wrap">
    <div class="header">
      <div class="head">
        <a @click="$router.back()" class="back"></a>
        {{ $t('lang.auth') }}
      </div>
    </div>
    <div class="verify_wrap">
      <form class="form" @submit.prevent="handleSubmit">
        <div class="tips">{{ $t('lang.auth_first') }}</div>
        <div class="item">
          <span class="info">{{ $t('lang.realname') }} </span>
          <input
            class="inp"
            type="text"
            name="name"
            v-model.trim="data.name"
            :placeholder="$t('lang.real_name')"
            :readonly="data.is_auth === 'Y'"
          />
        </div>
        <div class="item">
          <span class="info">{{ $t('lang.idcard') }} </span>
          <input
            class="inp"
            type="text"
            name="idcard"
            v-model.trim="data.idcard"
            :placeholder="$t('lang.input_idcard')"
            :readonly="data.is_auth === 'Y'"
          />
        </div>
        <div class="tishi">
          <img src="./images/tishi.png" alt="" />
          <p>{{ $t('lang.tips') }}</p>
        </div>
        <div class="warming">
          {{ $t('lang.auth_notice') }}
        </div>
        <button
          type="submit"
          class="btn basic_btn_v2"
          :class="data.name != '' && data.idcard != '' ? '' : 'no_touch'"
          v-if="data.is_auth === 'N'"
        >
          {{ $t('lang.auth_r') }}
        </button>
      </form>
    </div>
    <transition name="fade">
      <div class="mask" v-show="rz_success">
        <div class="rz_inner">
          <img src="./images/renzhengchenggong.png" alt="" />
          <p>{{ $t('lang.auth_success') }}</p>
          <div class="basic_btn_v2" @click="rz_success_handle">
            {{ $t('lang.confirm') }}
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      money_info: '',
      money: '',
      rz_success: false,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/info/info').then(res => {
        this.data = res.data;
      });
    },
    rz_success_handle() {
      this.rz_success = false;
      this.$router.replace({
        path: this.$router.history.current.query.redirect || '/user',
      });
    },
    handleSubmit() {
      if (!this.data.name) {
        this.$notify({
          message: this.$t('lang.input_name'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.idcard) {
        this.$notify({
          message: this.$t('lang.input_idcard'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/user/info/auth', {
        ...this.data,
      }).then(() => {
        this.rz_success = true;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.auth_wrap {
  background: #f7f8f9;
  min-height: 100vh;
}
.verify_wrap .form {
  width: 100%;
  padding: 20px 25px;
  background-color: #ffffff;
  // margin-top: 12px;
  .tips {
    width: 100%;
    height: 21px;
    line-height: 28px;
    font-size: 17px;
    color: #000000;
    margin-bottom: 20px;
    font-weight: 400;
  }
}

.verify_wrap .form .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  // -webkit-box-align: center;
  // -ms-flex-align: center;
  // align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  border-bottom: 1px solid #e3e4e5;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 22px;
  padding: 8px 0;
  background-color: #ffffff;
}
.item .info {
  color: #b1b1b1;
  margin-bottom: 10px;
  width: 105px;
}
.verify_wrap .form .item .inp {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  text-align: left;
  font-size: 15px;
}

.warming {
  font-size: 13px;
  line-height: 18px;
  color: #b1b1b1;
  margin-top: 13px;
}

.btn {
  display: block;
  width: 90.6%;
  height: 47px;
  line-height: 47px;
  margin-top: 61px;
}

.rz_inner {
  width: 77.6%;
  padding: 39px 60px 26px 60px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 80px;
  }
  p {
    font-size: 17px;
    color: #000;
    line-height: 24px;
    margin: 32px auto 39px auto;
  }
  div {
    height: 40px;
    line-height: 40px;
    width: 100%;
  }
}
// .header .head{
//     background: url(./images/zxbg.png) center no-repeat;
//   }
.tishi {
  margin-top: 13px;
  display: flex;
  align-items: center;
  img {
    margin-right: 8px;
    width: 15px;
    height: 15px;
  }
  p {
    font-size: 13px;
    color: #333;
  }
}
.basic_btn_v2 {
  border-radius: 25px !important;
  background: linear-gradient(to right, #22bfbe, #19c099) !important;
}
</style>
