<template>
  <div class="content_bg">
    <div class="header">
      <div class="head">
        <a @click="$router.back('/address')" class="back"></a>
        {{ $t('lang.edit_address') }}
      </div>
    </div>
    <form class="form">
      <div class="item">
        <span class="icon icon_name">{{ $t('lang.name') }} </span>
        <input
          type="text"
          class="inp inp_name"
          v-model.trim="address.name"
          :placeholder="$t('lang.name')"
        />
      </div>
      <div class="item">
        <span class="icon icon_name">{{ $t('lang.mobile') }} </span>
        <input
          type="text"
          class="inp inp_name"
          v-model.trim="address.tel"
          :placeholder="$t('lang.input_mobile')"
        />
      </div>
      <div class="item">
        <span class="icon icon_number">{{ $t('lang.area') }} </span>
        <input
          type="text"
          class="inp inp_number"
          v-model.trim="address.addressDetail"
          :placeholder="$t('lang.address')"
        />
      </div>
    </form>
    <!-- <van-address-edit
      :area-list="areaList"
      :show-postal="false"
      :show-delete="false"
      :show-detail="false"
      show-search-result
      :area-columns-placeholder="[
        $t('lang.please_choose'),
        $t('lang.please_choose'),
        $t('lang.please_choose'),
      ]"
      :address-info="address"
      @save="onSave"
      @delete="onDelete"
    > -->
