            <div
              class="item"
              v-for="(item, index) in config.tags"
              @click="choose_tag(index)"
              :class="tag_default == index ? 'cur' : ''"
              :key="item"
            >
              {{ item }}
            </div>
            <div class="add_item">
              <input
                type="text"
                v-model.trim="write_type"
                :placeholder="$t('lang.input_tags')"
                maxlength="5"
              />
              <div
                class="write_ok"
                :class="write_type == '' ? '' : 'cur'"
                @click="sub_type"
              >
                <van-icon name="success" size="16" />
              </div>
            </div>
          </div>
        </div> -->

      <div class="moren">
        <div class="default_icon" @click="save()">
          <i class="def_icon" :class="is_default === 'Y' ? 'default' : ''"></i>
          {{ $t('lang.default_add') }}
          <!--  -->
        </div>
      </div>
    </template>
    <!-- </van-address-edit> -->
    <div class="button-wrap">
      <div class="button-btn" @click="onSave()">
        {{ $t('lang.submit') }}
      </div>
    </div>
  </div>
</template>

