  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
    $(
      '.van-cell.van-cell--center.van-switch-cell.van-address-edit__default .van-cell__title'
    ).append('<br/><span></span>');
  },
  methods: {
    save(val) {
      if (this.is_default == 'Y') {
        this.is_default = 'N';
      } else {
        this.is_default = 'Y';
      }
    },

    sub_type() {
      // 
      if (this.write_type == '') {
        return;
      }
      this.config.tags.push(this.write_type);
      this.write_type = '';
    },
    onSave() {
      Fetch('/user/address/save', {
        id: this.$router.history.current.params.id
          ? this.$router.history.current.params.id
          : 0,
        tags: JSON.stringify(this.config.tags),
        tag: this.config.tags[this.tag_default],
        // addressDetail: this.address,
        // address: this.address,
        name: this.address.name,
        tel: this.address.tel,
        province: '0',
        city: '0',
        county: '0',
        addressDetail: this.address.addressDetail,
        areaCode: '0',
        postalCode: '0',
        isDefault: this.is_default,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.update_success'),
        });
        this.$router.back();
      });
    },
    onDelete() {
      Fetch('/user/address/remove', {
        id: this.$router.history.current.params.id,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.update_success'),
        });
        this.$router.back();
      });
    },
    choose_tag(i) {
      this.tag_default = i;
    },
    start() {
      var that = this;
      if (this.$router.history.current.params.id) {
        Fetch('/user/address/view', {
          id: this.$router.history.current.params.id,
        }).then(res => {
          if (res.data.view.tags) {
            this.config = {
              tags: res.data.view.tags,
            };
            res.data.view.tags.forEach(function(k, v) {
              if (k == res.data.view.tag) {
                that.tag_default = v;
              }
            });
          } else {
            this.config = {
              tags: res.data.config.tags,
            };
          }
          this.address = {
            id: res.data.view.id,
            name: res.data.view.name,
            tel: res.data.view.tel,
            province: res.data.view.province,
            city: res.data.view.city,
            county: res.data.view.county,
            addressDetail: res.data.view.address,
            areaCode: res.data.view.area_code,
            postalCode: res.data.view.postal_code,
            isDefault: false,
          };

          if (res.data.view.is_default === 'Y') {
            this.address.isDefault = true;
          } else {
            this.is_default = res.data.view.is_default;
          }
        });
      } else {
        this.config = {
          tags: [
            this.$t('lang.house'),
            this.$t('lang.company'),
            this.$t('lang.school'),
          ],
        };
      }
    },
  },
};
</script>

