<template>
  <div class="point-page default-page">
    <div>
      <van-nav-bar
        :title="$t('lang.store')"
        class="header-nav"
        fixed
        @click-left="$router.back()"
      />
    </div>
    <div>
      <van-tabs
        class="ponit-tab"
        v-model="active"
        title-inactive-color="#999999"
      >
        <van-tab v-for="item in classify" :title="item.name" :key="item.id">
          <v-touch
            class="goods-box clearfix"
            @swipeleft="onSwipeLeft"
            @swiperight="onSwipeRight"
            :swipe-options="{ direction: 'horizontal' }"
          >
            <!-- :finished-text="$t('lang.no_more')" -->
            <van-list v-model="loading" :finished="finished" @load="getList">
              <listItem
                class="goods-item"
                v-for="item in list"
                :key="item.id"
                :image="item.thumb"
                :con="item.title"
                :point="item.credit"
                :status="item.status"
                :id="item.id"
                @tap="onClick(item)"
              />
            </van-list>
          </v-touch>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { Tabs, Tab, NavBar, List, Cell } from 'vant';
import point from '@/views/index/component/point';
export default {
  components: {
    VanTab: Tab,
    VanTabs: Tabs,
    [NavBar.name]: NavBar,
    [List.name]: List,
    [Cell.name]: Cell,
    listItem: point,
  },
  data() {
    return {
      active: '',
      classify: [],
      list: [],
      i: 0,
      pageNum: 1,
      loading: false,
      finished: false,
    };
  },
  watch: {
    active() {
      this.getList();
    },
  },
  created() {
    this.$parent.footer(true, 'focus');
    this.getConfig();
  },
  methods: {
    getConfig() {
      // 
      this.loading = true;
      this.$Fetch('/goods/info/config').then(res => {
        this.classify = res.data.cats;
      });
    },
    getList() {
      if (cat_id === '') return;
      this.loading = true;
      const cat_id = this.classify[this.active].id;
      this.list = [];
      return this.$Fetch('/goods/info/search', { cat_id }, {}, false).then(
        res => {
          this.loading = false;
          this.finished = true;
          this.list = this.list.concat(res.data.list);
        }
      );
    },
    onClick(item) {
      this.$router.push({ name: 'point_detail', query: { id: item.id } });
    },
    onSwipeLeft(e) {
      if (this.active === this.classify.length - 1) return;
      this.active++;
    },
    onSwipeRight(e) {
      e.preventDefault();
      if (this.active === 0) return;
      this.active--;
    },
  },
};
</script>

