<template>
  <div class="point-confirm">
    <div class="header">
      <van-nav-bar
        :title="$t('lang.confirm_order')"
        left-arrow
        class="header-nav"
        @click-left="$router.back()"
      />
    </div>
    <div class="order_address">
      <div class="no_address" @click="onChangeAddress" v-if="!name">
        +{{ $t('lang.add_address') }}
      </div>
      <div class="has_address">
        <img src="./images/address.png" alt="" />
        <div class="add_content">
          <p>
            <span>{{ name }}</span>
            <span>{{ mobile }}</span>
            <i>{{ $t('lang.default') }} </i>
          </p>
          <div class="details_add">{{ address }}</div>
        </div>
        <Icon name="arrow" class="right_row" @click="$router.push(`address`)" />
      </div>
    </div>
    <div class="goods-info">
      <img :src="goodsImage" alt />
      <div class="goods-title">
        <div>{{ goodsTitle }}</div>
        <div class="newprice">
          <div class="newprice1">
            <img src="./images/pro.png" alt="" />
            <p>{{ goodsCredit }}</p>
          </div>
          <!-- <div class="newprice2">
            <p> 1000</p>
          </div> -->
        </div>
      </div>
    </div>
    <Popup v-model="successPopup" round class="sucess-wrap">
      <div class="icon">
        <!-- <Icon name="checked" size="71" color="#0076FF" /> -->
        <img src="./images/success.png" alt="" />
      </div>
      <div class="info">{{ $t('lang.exchange_success') }}</div>
      <div class="button-default" @click="onClose">{{ $t('lang.ok') }}</div>
    </Popup>
    <div class="button-wrap">
      <div class="pay_tips">
        <!-- {{ $t('lang.pay') }} -->
        <span>
          <img src="./images/pro.png" alt="" />
          {{ goodsCredit }}
        </span>
        <!-- <span class="tip">{{ $t('lang.integral') }} </span> -->
      </div>
      <!-- <div class="button-btn" @click="onConfirm">{{ $t('lang.confirm') }}</div> -->
      <div class="button-btn" @click="onConfirm">{{ $t('lang.confirm') }}</div>
    </div>
  </div>
</template>

<script>
import { NavBar, Cell, Popup, Icon } from 'vant';
export default {
  components: {
    [NavBar.name]: NavBar,
    // Cell,
    // CellGroup,
    Popup,
    Icon,
  },
  data() {
    return {
      goodsImage: '',
      goodsTitle: '',
      goodsCredit: '',
      successPopup: false,
      name: '',
      mobile: '',
      address: '',
      id: '',
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.$parent.footer(false, 'index');
    this.getAddress();
    this.getInfo(this.id);
  },
  methods: {
    onChangeAddress() {
      this.$router.push({ name: 'address' });
    },
    onClose() {
      this.successPopup = false;
    },
    getInfo(id) {
      this.$Fetch('/goods/info/view', { id }).then(res => {
        const data = res.data.view;
        if (data.status !== 'Y') {
          this.$Dialog
            .alert({ message: this.$t('lang.product_offline') })
            .then(() => {
              this.$router.back();
            });
        }
        this.goodsImage = data.thumb;
        this.goodsTitle = data.title;
        this.goodsCredit = data.credit;
      });
    },
    getAddress() {
      this.$Fetch('/user/address/search').then(res => {
        const data = res.data.list;
        const def = data.find(item => item.is_default === 'Y');
        if (def) {
          this.name = def.name;
          this.mobile = def.tel;
          this.address =
            def.province +
            ' ' +
            def.city +
            ' ' +
            def.county +
            ' ' +
            def.address;
        }
      });
    },
    onConfirm() {
      if (!this.name) return;
      const req = {
        id: this.id,
        name: this.name,
        address: this.address,
        mobile: this.mobile,
      };
      this.$Fetch('/goods/order/apply', req).then(res => {
        console.log(456);
        this.successPopup = true;
      });
    },
  },
};
</script>

