<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Empty } from 'vant';

Vue.use(Empty);
export default {
  data() {
    return {
      sign_show: false,
      sign_rule: '',
      running_days: 0,
      reward_set_list: [],
      month_signs: {},
      is_check: false,
      sign_list: [],
      sign_bg: '',
    };
  },
  components: {},
  mounted() {
    this.$parent.footer(false);
    this.refresh();
  },
  methods: {
    refresh() {
      Fetch('/user/info/checkinfo').then(res => {
        this.sign_list = [];
        this.running_days = res.data.running_days;
        this.is_check = res.data.today_sign === 1 ? true : false;
        this.month_signs = res.data.month_signs;
        this.sign_bg = res.data.sign_bg;

        Object.keys(this.month_signs).forEach(key => {
          if (this.month_signs[key].length !== 0) {
            this.sign_list.push(this.month_signs[key]);
          }
        });
        this.reward_set_list = [];
        Object.keys(res.data.reward_set).map(keys => {
          console.log(keys);
          this.reward_set_list.push({
            type: res.data.reward_set[keys].type,
            value: res.data.reward_set[keys].value,
            days: keys,
          });
        });
        console.log(this.reward_set_list);
      });
    },
    handleSign() {
      Fetch('/user/info/checkin').then(res => {
        this.refresh();
      });
    },
  },
};
</script>

