        </router-link>
        <router-link to="/notice" class="icon_msg">
          <p class="msg_num" v-show="msg_num > 0 || msg_num == '9+'">
            {{ msg_num }}
          </p>
          <img src="./images/msg1.png" alt />
        </router-link>
      </div>
      <div class="money_box">
        <div class="money_item">
