            		<div class="icon_list">
            			<div class="router" @click="router_go(0)"><img src="./images/gongsihuanjing2.png"/><span v-if="loading" v-html="about_font(0)"></span></div>
            			<div class="router" @click="router_go(2)"><img src="./images/gongsijianjie2.png"/><span v-if="loading" v-html="about_font(2)"></span></div>
            			<div class="router" @click="router_go(6)"><img src="./images/yinhangcunguan2.png"/><span v-if="loading" v-html="about_font(6)"></span></div>
            			<div class="router" @click="router_go(7)"><img src="./images/yingyezhizhao2.png"/><span v-if="loading" v-html="about_font(7)"></span></div>
            			<div class="router" @click="router_go(8)"><img src="./images/lianxiwomen2.png"/><span v-if="loading" v-html="about_font(8)"></span></div>
            		</div>
            	</div>
            	<div class="row_line"></div>
      </div>-->
      <!-- <div class="logout_btn" @click="logout"></div> -->
    </div>
    <!-- <Kefu /> -->
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { NoticeBar } from 'vant';
import { Cell } from 'vant';
// import Kefu from '@/components/kefu';
Vue.use(NoticeBar);

export default {
  name: 'info',
  components: {
    Cell,
    // Kefu,
  },
  data() {
    return {
      data: {},
      item_code: [],
      loading: false,
      show: false,
      msg_num: 0,
      config: {
        user_contract_link: '/',
        user_contract_ys_link: '/',
      },
    };
  },
  created() {
    if (this.$parent.getFooterType() === 'n2') {
      this.$router.replace('/user');
    }
    this.$parent.footer(true, 'info');
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/info/index').then(res => {
        this.data = res.data;
      });
      Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.config = res.data;
        this.show = true;
      });
      var that = this;
      Fetch('/article/about/search').then(res => {
        that.item_code = res.data.list;
        that.loading = true;
      });
      Fetch('/user/noticeread/search').then(res => {
        var msg_arr = 0;
        res.data.list.forEach(function(k, v) {
          if (!k.is_read) {
            msg_arr += 1;
          }
        });

        this.msg_num = msg_arr;
        if (msg_arr > 9) {
          this.msg_num = '9+';
        }
      });
    },
    logout() {
      Fetch('/api/api/logout').then(() => {
        this.$parent.setFooterType('n1');
        localStorage.setItem('reset_time', 0);
        localStorage.setItem('ssid', '');
        this.$router.replace('/login');
      });
    },
    router_go(r) {
      console.log('r :>> ', r);
      this.$router.push('/art/' + this.item_code[r].code);
    },
    about_font(n) {
      return this.item_code[n].title;
    },
  },
};
</script>

