<template>
  <div class="bank_big_wrap">
    <div class="header">
      <div class="head">
        <a @click="$router.back()" class="back"></a>
        {{ $t('lang.add_bankcard') }}
      </div>
    </div>
    <form class="form">
      <div class="bank_tips">{{ $t('lang.add_bankcard_tag') }}</div>
      <div class="item">
        <span class="icon icon_name">{{ $t('lang.banekname') }} </span>
        <input
          type="text"
          class="inp inp_name"
          v-model.trim="bank.banekname"
          :placeholder="$t('lang.banekname')"
        />
        <!-- <input
          type="text"
          class="inp inp_name"
          @click="show_bank"
          v-model.trim="bank.name"
          readonly="readonly"
          :placeholder="$t('lang.banekname')"
        /> -->
      </div>
      <div class="item">
        <span class="icon icon_name">{{ $t('lang.name') }} </span>
        <input
          type="text"
          class="inp inp_name"
          v-model.trim="bank.name"
          :placeholder="$t('lang.name')"
        />
      </div>
      <div class="item">
        <span class="icon icon_number">{{ $t('lang.cardnum') }} </span>
        <input
          type="number"
          class="inp inp_number"
          v-model.trim="bank.card"
          :placeholder="$t('lang.input_cardnum')"
        />
      </div>
    </form>
    <div
      @click="handleSubmit"
      class="btn basic_btn_v2"
      :class="bank.name != '' && bank.card != '' ? '' : 'no_touch'"
    >
      {{ $t('lang.add_r') }}
    </div>
    <transition name="slide-fade">
      <div class="mask bank_mask" v-show="bank_mask">
        <div class="bank_inner">
          <div class="first_str_box">
            <span
              @click="scroll_view(sv)"
              :class="sv == now_cur ? 'cur' : ''"
              v-for="(sk, sv) in all_bank_list"
              :key="sv"
              >{{ sv }}</span
            >
          </div>
          <div class="bank_title">
            {{ $t('lang.choose_bank') }}
            <span @click="close_mask"></span>
          </div>
          <div class="search_box">
            <div class="search_bg">
              <span></span>
              <input
                type="text"
                @input="search($event)"
                class="search_inp"
                :placeholder="$t('lang.search')"
              />
            </div>
          </div>
          <div class="bank_list">
            <div class="item" v-for="(k, v) in all_bank_list" :key="v">
              <p :class="'s' + v">{{ v }}</p>
              <div
                v-for="(item, index) in k"
                @click="choose_bank(item)"
                :key="index"
              >
                {{ item }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  data() {
    return {
      data: {},
      bank: {
        card: '',
        name: '',
      },
      now_cur: 'A',
      bank_mask: false,
      first_str: {},
      all_bank_list: {},
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    close() {
      this.is_show = false;
    },
    showAdd() {
      this.is_show = true;
    },
    start() {
      var that = this;
      Fetch('/user/bank/search')
        .then(res => {
          this.data = res.data.config.code;
        })
        .then(function() {
          var index_arr = [];
          var val_arr = [];
          for (var i in that.data) {
            index_arr.push(i);
            val_arr.push(that.data[i]);
          }
          var szm = [];
          index_arr.forEach(function(k, v) {
            szm.push(k[0]);
          });
          var szm_arr = szm.reduce((o, k) => {
            k in o ? o[k]++ : (o[k] = 1);
            return o;
          }, {});
          szm_arr = Object.keys(szm_arr).sort();

          var all_bank = {};

          szm_arr.forEach(function(k, v) {
            all_bank[k] = [];
          });

          for (var item in all_bank) {
            for (var b = 0; b < index_arr.length; b++) {
              if (index_arr[b][0] == item) {
                all_bank[item].push(val_arr[b]);
              }
            }
          }

          that.all_bank_list = all_bank;

          // 
        });
    },
    choose_bank(item) {
      this.bank.name = item;
      this.bank_mask = false;
      $('.inp_name').blur();
    },
    close_mask() {
      this.bank_mask = false;
      $('.inp_name').blur();
    },
    show_bank() {
      this.bank_mask = true;
      setTimeout(function() {
        var b_height =
          $('.bank_inner').height() -
          $('.bank_title').height() -
          $('.search_box').height();
        $('.bank_list').height(b_height);
      }, 350);
    },
    handleSubmit() {
      if (!this.bank.name.replace(/\s/g, '')) {
        this.$notify({
          message: this.$t('lang.input_bankname'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      if (!/^[0-9]*$/.test(this.bank.card)) {
        this.$notify({
          message: this.$t('lang.input_right_bankcard'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      Fetch('/user/bank/add', {
        ...this.bank,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.add_success'),
        });
        this.$router.back();
      });
    },
    scroll_view(ele) {
      this.now_cur = ele;
      var scrollToContainer = $('.s' + ele);
      var mainContainer = $('.bank_list');
      mainContainer.animate(
        {
          scrollTop:
            scrollToContainer.offset().top -
            mainContainer.offset().top +
            mainContainer.scrollTop(),
        },
        1000
      );
    },
    search(ev) {
      var val = $.trim(ev.target.value);
      if (val == '') {
        return;
      }
      var all_list = this.all_bank_list;
      var _index = null;
      for (var item in all_list) {
        for (var i = 0; i < all_list[item].length; i++) {
          if (all_list[item][i].indexOf(val) != -1 || item.indexOf(val) != -1) {
            _index = item;
          }
        }
      }
      if (_index) {
        var scrollToContainer = $('.s' + _index);
        var mainContainer = $('.bank_list');
        mainContainer.animate(
          {
            scrollTop:
              scrollToContainer.offset().top -
              mainContainer.offset().top +
              mainContainer.scrollTop(),
          },
          1000
        );
      }
    },
  },
};
</script>

<style lang="less" scoped>
.bank_big_wrap {
  min-height: 100%;
  background-color: #fff;
}

.bank_tips {
  width: 100%;
  height: 21px;
  line-height: 28px;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 1px;
  color: #000000;
  margin-bottom: 20px;
}
.form {
  margin: auto;
  margin-top: 12px;
  width: 93%;
  padding: 20px 15px;
  background-color: #ffffff;
  box-sizing: border-box;
  .item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-bottom: 1px solid #e3e4e5;
    font-size: 15px;
    line-height: 22px;
    padding: 10px 0;
    background-color: #ffffff;
    span {
      width: 80px;
      color: #666;
    }
    input {
      margin-top: 15px;
    }
    input::-webkit-input-placeholder {
      color: #999;
    }
    // &:last-of-type {
    //   border: none;
    // }
  }
}
.basic_btn_v2 {
  width: 90.66%;
  height: 47px;
  line-height: 47px;
  margin-top: 56px;
  border-radius: 25px;
  background: linear-gradient(to right, #22bfbe, #19c099);
  box-shadow: 0 5px 10px #9ee3e1;
  opacity: 1;
}
.bank_mask {
  width: 100%;
  max-width: 23.4375rem;
  margin: 0 auto;
}
.bank_inner {
  width: 100%;
  height: 80%;
  background-color: #ebebeb;
  border-radius: 16px 16px 0 0;
  padding-top: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
  .bank_list {
    height: 75%;
    width: 100%;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  .bank_title {
    width: 100%;
    position: relative;
    height: 59px;
    line-height: 59px;
    text-align: center;
    font-size: 15px;
    color: #333333;
    span {
      position: absolute;
      left: 18px;
      width: 18px;
      height: 18px;
      top: 22px;
      background: url(images/close1.png) no-repeat center center;
      background-size: 100% 100%;
    }
  }
  .search_box {
    width: 100%;
    height: 43px;
    background-color: #ebebeb;
    position: relative;
    z-index: 2;
  }
  .search_bg {
    width: 96.36%;
    height: 33px;
    margin: 0 auto;
    border-radius: 3px;
    background-color: #ffffff;
    line-height: 33px;
    color: #000;
    font-size: 15px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
      width: 17px;
      height: 15px;
      // border-radius: 50%;
      // border: 1px solid #8a8a8e;
      margin-left: 9px;
      background: url(./images/search.png) no-repeat;
      background-size: contain;
    }
    input {
      margin-left: 11px;
      width: 100%;
    }
    input::-webkit-input-placeholder {
      color: #b1b1b1;
    }
  }
  .item {
    width: 100%;
    p {
      font-size: 12px;
      color: #939393;
      padding: 0 18px;
      height: 42px;
      line-height: 42px;
    }
    div {
      height: 44px;
      line-height: 44px;
      font-size: 14px;
      color: #333333;
      padding: 0 18px;
      border-bottom: 1px solid #f3f3f3;
      background-color: #ffffff;
    }
  }
}
.first_str_box {
  width: 16px;
  height: 63.88%;
  position: absolute;
  right: 23px;
  bottom: 26px;
  display: flex;
  z-index: 10;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  span {
    font-size: 12px;
    line-height: 16px;
    height: 16px;
    width: 16px;
    text-align: center;
    &.cur {
      border-radius: 50%;
      background-color: #e03325;
      color: #ffffff;
    }
  }
}
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateY(100%);
  opacity: 0;
}
.header .head {
  background: url(./images/head_bg1.png) center no-repeat;
}
.first_str_box span.cur {
  background: #22bfbe;
}
.bank_inner .item p {
  background: #fff;
  color: #22bfbe;
  font-size: 15px;
  font-weight: bold;
}
.bank_inner {
  background: #fff;
}
.bank_inner .search_box {
  background: #fff;
}
.bank_inner .search_bg {
  margin: auto;
  width: 90%;
  border-radius: 15px;
  border: 1px solid #dbdbdb;
}
</style>
