<template>
  <div class="item_body">
    <div class="header">
      <div class="head">
        <!-- {{ $t('lang.project_list') }} -->
        {{ $t('lang.station') }}
      </div>
    </div>
    <itemsList :list="data.list" />
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Circle } from 'vant';
import itemsList from '@/components/itemsList';

Vue.use(Circle);
export default {
  name: 'index',
  components: {
    itemsList,
  },
  data() {
    return {
      data: {},
    };
  },
  created() {
    this.$parent.footer(true, 'item');
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/item/dq/search').then(res => {
        this.data = res.data;
      });
    },
    schedule(schedule) {
      return {
        width: schedule + '%',
      };
    },
    goToDetail(id) {
      this.$router.replace('/item/' + id);
    },
    schedule_left(schedule) {
      if (schedule > 80) {
        return {
          left: schedule - 30 + '%',
          color: '#fff',
        };
      } else {
        return {
          left: schedule.slice(0, 5) + '%',
        };
      }
    },
  },
};
</script>

<style lang="less" scoped>
.item_body {
  background: #f1f1f1;
  padding-bottom: 65px;
  min-height: 100vh;
}
/deep/ .van-circle__text {
  color: #00b389;
  font-size: 14px;
}
</style>
