<template>
  <div class="point-page default-page">
    <div>
      <van-nav-bar
        :title="$t('lang.notice_plat')"
        class="header-nav"
        fixed
        @click-left="$router.back()"
        left-arrow
      />
    </div>
    <div class="bigbg">
      <noticeList :list="focus_list" title="" />
    </div>
  </div>
</template>

<script>
import { NavBar, List, Cell } from 'vant';
import noticeList from '@/components/noticeList';
export default {
  components: {
    [NavBar.name]: NavBar,
    [List.name]: List,
    [Cell.name]: Cell,
    noticeList,
  },
  data() {
    return {
      active: '',
      classify: [],
      list: [],
      point_list: [],
      i: 0,
      pageNum: 1,
      loading: false,
      isShop: 'off',
      finished: false,
      focus_list: [],
    };
  },
  watch: {
    active() {
      this.getList();
    },
  },
  created() {
    this.$parent.footer(false, 'focus');
    this.getConfig();
  },
  mounted() {
    this.getNotice();
  },
  methods: {
    getNotice() {
      this.$Fetch('/article/about/search', { type: 'notice' }).then(res => {
        this.focus_list = res.data.list.filter(item => {
          console.log(item.is_show_index);
          return item.is_show_index === 'Y';
        });
      });
    },
    GoDetail(id) {
      console.log('id :>> ', id);
      this.$router.push({ name: 'point_detail', query: { id } });
    },
    onMore() {
      this.$router.push({ name: 'point_mall' });
    },
    getConfig() {
      // 
      this.loading = true;
      this.$Fetch('/goods/info/config').then(res => {
        this.classify = res.data.cats;
      });
    },
    getList() {
      if (cat_id === '') return;
      this.loading = true;
      const cat_id = this.classify[this.active].id;
      this.list = [];
      return this.$Fetch('/goods/info/search', { cat_id }, {}, false).then(
        res => {
          this.loading = false;
          this.finished = true;
          this.list = this.list.concat(res.data.list);
        }
      );
    },
    onClick(item) {
      this.$router.push({ name: 'point_detail', query: { id: item.id } });
    },
    onSwipeLeft(e) {
      if (this.active === this.classify.length - 1) return;
      this.active++;
    },
    onSwipeRight(e) {
      e.preventDefault();
      if (this.active === 0) return;
      this.active--;
    },
  },
};
</script>

