<template>
  <div class="content_bg">
    <div class="header">
      <div class="head">
        <a @click="$router.back()" class="back">{{ $t('lang.cancel') }} </a>
        {{ $t('lang.set_nickname') }}
      </div>
    </div>
    <h5 class="nick_tips">{{ $t('lang.input_nickname') }}</h5>
    <div class="main">
      <div class="item">
        <input type="text" v-model.trim="nickname" placeholder="" />
      </div>
    </div>
    <div
      class="submit basic_btn_v2"
      @click="setname"
      :class="nickname != '' ? '' : 'no_touch'"
    >
      {{ $t('lang.chonfirm_edit') }}
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'setup',
  components: {},
  data() {
    return {
      nickname: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/info/index').then(res => {
        this.nickname = res.data.nick_name;
      });
    },
    setname() {
      Fetch('/user/info/upavatar', {
        nick_name: this.nickname,
      }).then(res => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.edit_success'),
        });
        this.$router.back();
      });
    },
  },
};
</script>

