<template>
  <div
    class="forest_wrap"
    style="height: 100vh"
    :class="[data.background, treeImg]"
  >
    <!--  -->
    <div class="horn_box" v-show="horn_block">
      <i class="horn_icon"></i>
      <div class="horn_tips" id="horn_tips">
        <div class="horn_go" id="horn_go1">{{ horn_msg }}</div>
        <div class="horn_go" id="horn_go2" style="display: none;">
          {{ horn_msg }}
        </div>
      </div>
    </div>
    <!-- <div class="light"></div> -->
    <div class="top_box">
      <!-- <a @click="$router.push('/user')" class="left"></a> -->
    </div>
    <div class="side_btn_box">
      <span class="btn_box btn_box1" @click="$router.push('/sign')"></span>
      <span class="btn_box btn_box2" @click="$router.push('/jz')"></span>
      <span class="btn_box btn_box3" @click="record_show"></span>
      <span class="btn_box btn_box4" @click="$router.push('/kefu')"></span>
      <span class="btn_box btn_box5" @click="rule_click"></span>
    </div>
    <div class="remain_box">
      <div class="manure_box">
        <span class="money_icon" id="money_wrapper"></span>
        <span class="manure_info">{{ parseInt(data.fruit) }}</span>
      </div>
      <div class="manure_box">
        <span class="water_icon"></span>
        <span class="manure_info">{{ data.water }}</span>
      </div>
      <div class="manure_box">
        <span class="manure_icon"></span>
        <span class="manure_info">{{ data.manure }}</span>
      </div>
    </div>
    <div class="handle_box">
      <span class="handle_btn water" @click="water()"><i></i></span>
      <span class="handle_btn manure" @click="water('manure')"><i></i></span>
    </div>
    <!--  -->
    <div
      class="money_target"
      v-show="money_icon_show"
      @click="$router.push(jump_url)"
    >
      <div class="money_click"></div>
    </div>
    <div
      class="apple_box"
      id="apple_box"
      v-show="show_apple"
      @click="pluck($event)"
    ></div>
    <div class="tree_wrap">
      <div class="tree_box">
        <!-- <img class="tree" :src="treeImg"> -->
        <div
          class="water_bg"
          v-bind:class="{ handle_bg: show_water === true }"
        ></div>
        <div
          class="manure_bg"
          v-bind:class="{ handle_bg: show_manure === true }"
        ></div>
        <!-- <div class="apple_box" v-show="show_apple" @click="pluck">
                    <div class="apple_bg apple1"></div>
                    <div class="apple_bg apple2"></div>
                </div> -->
      </div>
    </div>
    <div class="tree_progress">
      <div
        class="progress_box"
        :class="
          data.type == 'tree0' || data.type == 'tree1' || data.type == 'tree2'
            ? 'mt8'
            : ''
        "
      >
        <div class="inner_pro" :style="{ width: progress }"></div>
      </div>
      <div class="water_proress icon_progress" style="display: none;">
        <span class="tree_pct">-61%</span>
        <span class="icon_water icon_inner"></span>
      </div>
      <div class="manure_proress icon_progress" style="display: none;">
        <span class="tree_pct">-61%</span>
        <span class="icon_manure icon_inner"></span>
      </div>
    </div>
    <transition name="fade">
      <div class="sign_pop" v-show="sign_ok">
        <div class="sign_inner">
          <div @click="close()" class="close_btn"></div>
          <div @click="close()" class="i_know"></div>
        </div>
      </div>
    </transition>

    <!--  -->
    <transition name="fade">
      <div class="rule_mask" v-show="rule_mask">
        <div class="rule_inner">
          <div class="water_time">
            <pre>{{ water_time }}</pre>
          </div>
          <div class="water_rule">
            <pre>{{ water_rule }}</pre>
          </div>
          <div @click="close()" class="rule_know"></div>
        </div>
      </div>
    </transition>

    <!--  -->
    <transition name="slide-fade">
      <div class="shop_mask mask" v-show="shop_show">
        <div class="close_shop" @click="close_shop"></div>
        <div class="change_shop">
          <div class="shop_title">
            <div class="vip_icon" :class="`vip${category_data.level}`"></div>
            <div class="record_icon" @click="$router.push('/record')"></div>
            <div class="rule_icon" @click="more"></div>
          </div>
          <div class="shop_list">
            <div class="shop_left">
              <div
                class="left_list"
                v-for="(item, index) in category_data.category"
                @click="cut_shop(index)"
                v-bind:class="{ cur: index === dh_index }"
                :key="index"
              >
                <img :src="item.category_image" alt="" />
              </div>
            </div>
            <div class="shop_right">
              <div
                v-for="(item, j) in category_data.category"
                v-bind:style="{ display: j === dh_index ? 'block' : 'none' }"
                :key="j"
              >
                <div
                  class="right_list"
                  v-for="(item2, j_num) in item.list"
                  :key="j_num"
                >
                  <div class="shop_pic">
                    <img :src="item2.thumb" alt="" />
                  </div>
                  <div class="shop_detail">
                    <p class="list_title">{{ item2.name }}</p>
                    <div class="shop_gb">
                      <div class="vip_icon" :class="`vip${item2.level}`">
                        <span>{{ item2.price }}{{ $t('lang.fcoins') }}</span>
                      </div>
                      <div
                        class="vip_icon2 vip_icon"
                        :class="`vip${item2.next_level}`"
                      >
                        <span
                          >{{ item2.next_level_price
                          }}{{ $t('lang.fcoins') }}</span
                        >
                      </div>
                    </div>
                    <div class="dh_num">
                      <div class="add_del">
                        <span @click="del(j, j_num)"></span>
                        <span class="num">{{ dh_num[j][j_num] }}</span>
                        <span @click="add(j, j_num)"></span>
                      </div>
                      <div
                        class="dh_btn"
                        :class="[
                          { dh_yes: item2.status == 1 },
                          { dh_no: item2.status == 2 },
                          { dh_over: item2.status == 3 },
                        ]"
                        @click="category_btn(item2.id, dh_num[j][j_num])"
                      >
                        {{ dh_status(item2.status) }}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <!--  -->
    <transition name="fade">
      <div class="rule_pop" v-show="rule_dh">
        <div class="sign_inner">
          <div class="rule_tips">
            <pre>{{ rule_msg }}</pre>
          </div>
          <div @click="close()" class="i_know"></div>
        </div>
      </div>
    </transition>
    <!--  -->
    <div class="mask" v-show="add_res">
      <div class="close_add" @click="close">{{ $t('lang.close') }}</div>
      <div class="go_address" @click="$router.push('/address')">
        {{ $t('lang.add_address') }}
      </div>
    </div>
    <!--  -->
    <div class="hide_tree_box">
      <img v-for="(item, index) in hide_tree" :src="item" alt="" :key="index" />
    </div>
    <!-- mp3 -->
    <audio :src="mp3" id="mp3"></audio>
    <!--  -->
    <transition name="fade">
      <div class="new_sign_pop mask" @click="close" v-show="new_sign_ok">
        <div class="new_sign_inner">
          <div class="sign_number">
            <span>+</span><span>{{ sign_money }}</span
            ><span>{{ $t('lang.coins') }}</span>
          </div>
        </div>
      </div>
    </transition>
    <!--  -->
    <div class="mask lc_mask" :class="show_delay_animate" v-show="only_money">
      <div class="max_mask">
        <div class="money_target">
          <div class="money_hand" :class="level_lc"></div>
          <div class="money_click"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'shop',
  data() {
    return {
      data: {},
      dh_index: 0,
      horn_block: false,
      category_data: {},
      is_sleep: false,
      show_water: true,
      show_manure: true,
      treeImg: '',
      dh_num: [],
      only_num: 1,
      sign_ok: false,
      new_sign_ok: true,
      shop_show: false,
      show_apple: false,
      rule_mask: false,
      add_res: false,
      only_money: false,
      money_icon_show: false,
      show_delay_animate: '',
      level_lc: 'lv2',
      horn_msg: '',
      jump_url: '/item',
      fly_go: '',
      hide_tree: [],
      progress: '0%',
      notice_num: 1,
      mp3: '',
      sign_money: 0,
      dh_btn_type: '',
      water_time: this.$t('lang.water_time'),
      water_rule: this.$t('lang.water_rule'),
      rule_dh: false,
      rule_msg: this.$t('lang.rule_msg'),
    };
  },
  computed: {
    dh_status() {
      return function(n) {
        if (n == 1) {
          return this.$t('lang.exchange');
        } else if (n == 2) {
          return this.$t('lang.has_exchange_f');
        } else if (n == 3) {
          return this.$t('lang.less_fcoins');
        }
      };
    },
  },
  created() {
    this.$parent.footer(true, 'tree');
    this.treeImg = 'tree1';
  },
  mounted() {
    setTimeout(function() {
      document.getElementById('app').style.marginBottom = 0;
    }, 100);
    this.mp3 = require('./image/didi.mp3');
    this.start();
    if (location.href.indexOf('openshop') != -1) {
      this.shop_show = true;
    }

    $(window).resize(function() {
      set();
    });
    function set() {
      var w = $(window).width();
      var h = $(window).height();
      var l = 0;
      var t = 0;
      l = w / 3.95;
      t = h / 2.4;
      if (w >= 750) {
        w = 750;
        l = 175;
      }
      $('.money_target').css({
        transform: 'scale(' + w / 414 + ')',
        'transform-origin': 'left top',
        '-webkit-transform': 'scale(' + w / 414 + ')',
        '-webkit-transform-origin': 'left top',
        position: 'absolute',
        top: t,
        left: l,
      });
    }
    set();
  },
  methods: {
    async fly(event, flyNum) {
      var that = this;
      const num = flyNum;
      const globalLeft = $('#money_wrapper').offset().left + 20;
      const globalTop =
        $('#money_wrapper').offset().top - $(document).scrollTop();
      const appleImage = require('./image/tree/apple.png');
      var mp3_ele = document.getElementById('mp3');

      for (let i = 1; i <= num; i++) {
        mp3_ele.play();
        await new Promise(reslove => {
          $(`<img src=${appleImage} style="width: 35px;" />`).fly({
            start: {
              left: event.pageX,
              top: event.pageY - $(document).scrollTop(),
            },
            end: {
              top: globalTop,
              left: globalLeft,
              width: 35,
              height: 35,
            },
            onEnd: function() {
              this.destroy();
              reslove();
            },
          });
        });
        mp3_ele.pause();
      }
      that.start();
    },
    move() {
      var m_l = 50;
      var width = document.getElementById('horn_go1').getBoundingClientRect()
        .width;
      var box = document.getElementById('horn_go1');
      var box2 = document.getElementById('horn_go2');
      box2.style.left = width + m_l + 'px';
      box2.style.display = 'block';
      var distance = 10;
      var distance2 = m_l + width;
      // 
      setInterval(function() {
        distance = distance - 1;
        distance2 = distance2 - 1;
        // 
        if (-distance >= width + m_l) {
          distance = width + m_l;
        }
        if (-distance2 >= width + m_l) {
          distance2 = width + m_l;
        }
        box.style.left = distance + 'px';
        box2.style.left = distance2 + 'px';
      }, 40);
    },
    //
    numFun(startNum, maxNum) {
      var that = this;
      let numText = startNum;
      let golb;
      function numSlideFun() {
        numText += 0.027;
        if (numText >= maxNum) {
          numText = maxNum;
          cancelAnimationFrame(golb);
        } else {
          golb = requestAnimationFrame(numSlideFun);
        }
        that.data.fruit = parseInt(numText);
      }
      numSlideFun();
    },
    del(j, i) {
      if (this.dh_num[j][i] <= 1) {
        return;
      }
      this.dh_num[j][i]--;
      this.only_num = this.dh_num[j][i];
      this.dh_num[j].push();
    },
    add(j, i) {
      this.dh_num[j][i]++;
      this.only_num = this.dh_num[j][i];
      this.dh_num[j].push();
    },
    cut_shop(i) {
      this.dh_index = i;
    },
    more() {
      this.rule_dh = true;
    },
    record_show() {
      this.shop_show = true;
    },
    close_shop() {
      this.shop_show = false;
    },
    rule_click() {
      this.rule_mask = true;
    },
    close() {
      this.sign_ok = false;
      this.rule_mask = false;
      this.rule_dh = false;
      this.add_res = false;
      this.new_sign_ok = false;
    },
    start() {
      this.show_delay_animate = 'delayfade';
      Fetch('/api/api/tree').then(res => {
        this.data = res.data;
        this.progress = res.data.tree_progress + '%';
        if (res.data.background == 'forest_wrap_night') {
          for (var i = 0; i < 7; i++) {
            this.hide_tree.push(require('./image/tree/night' + i + '.jpg'));
          }
        } else {
          for (let i = 0; i < 7; i++) {
            this.hide_tree.push(require('./image/tree/day' + i + '.jpg'));
          }
        }
        //  
        if (res.data.notice && this.notice_num == 1) {
          this.notice_num = 2;
          var that = this;
          this.horn_block = true;
          this.horn_msg = res.data.notice;
          setTimeout(function() {
            that.move();
          }, 1000);
        }

        this.treeImg = res.data.type;
        if (this.data.type == 'tree6') {
          this.show_apple = true;
        }
        if (res.data.tree_rule) {
          this.water_rule = res.data.tree_rule.note;
        }

        // 
        if (res.data.adv_icon.length != 0) {
          var show_lev = JSON.parse(res.data.adv_icon[0].condition);
          this.jump_url = res.data.adv_icon[0].jump_url;
          // if(localStorage.getItem("icon")==null){
          this.only_money = true;
          setTimeout(function() {
            that.only_money = false;
          }, 3000);
          // }
          this.money_icon_show = true;
          this.level_lc =
            'lv' + show_lev.name.replace(this.$t('lang.stage'), '');
          // localStorage.setItem("icon", "yes");
        }
        if (res.data.type == 'tree0') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-20px) translateY(-20px)'
          );
        }
        if (res.data.type == 'tree1') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-20px) translateY(-20px)'
          );
        }
        if (res.data.type == 'tree2') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-10px) translateY(-25px)'
          );
        }
        if (res.data.type == 'tree3') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-6px) translateY(-75px)'
          );
        }
        if (res.data.type == 'tree4') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-23px) translateY(-92px)'
          );
        }
        if (res.data.type == 'tree5') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-23px) translateY(-92px)'
          );
        }
        if (res.data.type == 'tree6') {
          $('.money_click,.money_hand').css(
            'transform',
            'translateX(-28px) translateY(-100px)'
          );
        }
      });

      // 
      Fetch('/goods/index/list', { ssid: this.ssid() }).then(res => {
        this.category_data = res.data;
        for (var i = 0; i < res.data.category.length; i++) {
          this.dh_num.push([]);
          for (var j = 0; j < res.data.category[i].list.length; j++) {
            this.dh_num[i].push(1);
          }
        }
        if (res.data.exchange_rules.content || res.data.exchange_rules.title) {
          this.rule_msg =
            res.data.exchange_rules.content || res.data.exchange_rules.title;
        }
      });
    },
    ssid() {
      return localStorage.getItem('ssid');
    },
    water(type = 'water') {
      if (!this.show_water || !this.show_manure) {
        return;
      }

      // if(type === 'water'){
      //     if (parseInt(this.data.water) < 1) {
      //         return;
      //     }
      // }else{
      //     if (parseInt(this.data.manure) < 1) {
      //         return;
      //     }
      // }

      Fetch('/user/tree/start', { type: type }).then(res => {
        this.$parent.setFooterType(res.data.footer);
        setTimeout(function() {
          document.getElementById('app').style.marginBottom = 0;
        }, 100);
        if (type === 'water') {
          this.show_water = false;
        } else {
          this.show_manure = false;
        }
        setTimeout(() => this.sleep(res.data.tree), 1950);
      });
    },
    sleep(img = 'tree1') {
      this.show_manure = true;
      this.show_water = true;
      this.start();
    },
    pluck(event) {
      if (this.data.type !== 'tree6') {
        return;
      }

      var that = this;

      Fetch('/user/tree/pluck').then(res => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message:
            this.$t('lang.this_get') + res.data.num + this.$t('lang.fruit'),
        });
        this.money_icon_show = false;
        var gs_num = parseInt(res.data.num);
        that.fly(event, gs_num);
        var old_gs = parseInt(that.data.fruit);
        that.numFun(old_gs, parseInt(gs_num + old_gs));
        this.show_apple = false;
      });
    },
    checkin() {
      Fetch('/user/info/checkin').then(res => {
        this.sign_money = res.data.reward;
        this.new_sign_ok = true;
      });
    },
    category_btn(id, num) {
      Fetch('/user/info/info').then(res => {
        if (res.data.is_auth == 'N') {
          this.$notify({
            message: this.$t('lang.auth_first'),
            color: '#FF3232',
            background: '#FFEAEA',
          });
          this.$router.push('/auth');
          return;
        } else {
          Fetch('/goods/index/shop', { id: id, num: num }).then(res => {
            this.$notify({
              background: '#EAF1FF',
              color: '#3292FF',
              message: this.$t('lang.exchange_success'),
            });
            this.start();
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.horn_box {
  width: 76.8%;
  height: 26px;
  background: rgba(255, 255, 255, 0.26);
  border-radius: 13px;
  position: absolute;
  top: 68px;
  right: 3%;
  line-height: 26px;
  font-size: 12px;
  padding-left: 39px;
  .horn_icon {
    width: 19px;
    height: 19px;
    position: absolute;
    left: 9px;
    top: 4px;
    background: url(./image/tree/horn.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .horn_tips {
    width: 95%;
    white-space: nowrap;
    height: 26px;
    overflow: hidden;
    line-height: 26px;
    position: relative;
    .horn_go {
      left: 0;
      position: absolute;
      top: 0;
    }
  }
}
.forest_wrap_night .horn_tips {
  color: #f7e7ca;
}
.horn_tips {
  color: rgb(245, 75, 42);
}
.hide_tree_box {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  opacity: 0;
  img {
    width: 100%;
    height: 100%;
  }
}

.money_target {
  width: 222px;
  height: 172px;
  position: absolute;
  left: 50%;
  top: 36%;
  z-index: 22;
  .money_hand {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 23;
    top: 0;
    left: 0;
  }
  .money_hand.lv1 {
    background: url(./image/tree/lv1.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_hand.lv2 {
    background: url(./image/tree/lv2.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_hand.lv3 {
    background: url(./image/tree/lv3.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_hand.lv4 {
    background: url(./image/tree/lv4.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_hand.lv5 {
    background: url(./image/tree/lv5.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_hand.lv6 {
    background: url(./image/tree/lv6.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_hand.lv7 {
    background: url(./image/tree/lv7.png) no-repeat center center;
    background-size: 100% 100%;
  }
  .money_click {
    width: 36px;
    height: 36px;
    position: absolute;
    z-index: 22;
    top: 116px;
    left: 74px;
    background: url(./image/tree/money_icon.png) 0 0 no-repeat;
    background-size: 1225%;
    -webkit-animation: garden-animation steps(11) 0.7s infinite forwards;
    animation: garden-animation steps(11) 0.7s infinite forwards;
  }
}
.new_sign_inner {
  width: 375px;
  height: 328px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -187px;
  margin-top: -169px;
  background: url(./image/tree/sign_pop.png) no-repeat center center;
  background-size: 100% 100%;
  .sign_number {
    width: 100%;
    position: absolute;
    top: 250px;
    text-align: center;
    color: #fff;
    height: 45px;
    line-height: 45px;
    span:first-child,
    span:nth-of-type(2) {
      font-size: 35px;
      height: 45px;
      display: inline-block;
    }
    span:nth-of-type(3) {
      font-size: 17px;
      margin-left: 12px;
      height: 45px;
      vertical-align: top;
      display: inline-block;
    }
  }
}
.max_mask {
  max-width: 750px;
  height: 100%;
  width: 100%;
  position: relative;
