<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/item/list/view', {
        id: this.$router.history.current.params.id,
      }).then(res => {
        this.data = res.data.view;
      });
    },
    look_ht() {
      this.$router.push('/contract/' + this.$router.history.current.params.id);
