<template>
  <div class="content">
    <div class="header">
      <div class="head">
        <a @click="$router.back()" class="back"></a>
        {{ $t('lang.find_pay_pw') }}
      </div>
    </div>
    <!-- <div class="resetpaypwd_wrap">
            <form class="form" @submit.prevent="handleSubmit">
                <label class="item">
                    <span class="info"></span>
                    <input type="password" class="inp oldpwd" v-model.trim="passwd" placeholder="">
                </label>
                <label class="item">
                    <span class="info"></span>
                    <input type="password" class="inp newpwd" v-model.trim="npasswd" placeholder="">
                </label>
                <label class="item">
                    <span class="info"></span>
                    <input type="password" class="inp confirm_newpwd" v-model.trim="snpasswd" placeholder="">
                </label>
                <button type="submit" class="sbtn"></button>
            </form>
        </div> -->
    <div class="resetpaypwd_wrap">
      <h5 class="set_tips">{{ $t('lang.finding_pay_pw') }}</h5>
      <form class="form" @submit.prevent="handleSubmit">
        <label class="item">
          <span class="info">{{ $t('lang.login_pw') }} </span>
          <input
            :type="oldpwd_type"
            class="inp oldpwd"
            v-model.trim="passwd"
            :placeholder="$t('lang.input_pw')"
          />
          <span
            class="right_icon"
            :class="oldpwd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_passwd"
          ></span>
        </label>
        <label class="item">
          <span class="info">{{ $t('lang.new_pw') }} </span>
          <input
            :type="newpwd_type"
            class="inp newpwd"
            v-model.trim="npasswd"
            :placeholder="$t('lang.input_new_pw')"
          />
          <span
            class="right_icon"
            :class="newpwd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_npasswd"
          ></span>
        </label>
        <label class="item no_border">
          <span class="info">{{ $t('lang.confirm_pw') }} </span>
          <input
            :type="confirm_newpwd_type"
            class="inp confirm_newpwd"
            v-model.trim="snpasswd"
            :placeholder="$t('lang.new_pw_match')"
          />
          <span
            class="right_icon"
            :class="confirm_newpwd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_snpasswd"
          ></span>
        </label>
        <button
          type="submit"
          class="sbtn basic_btn_v2"
          :class="
            passwd != '' && npasswd != '' && snpasswd != '' ? '' : 'no_touch'
          "
        >
          {{ $t('lang.confirm_edit') }}
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      passwd: '',
      npasswd: '',
      snpasswd: '',
      oldpwd_type: 'password',
      newpwd_type: 'password',
      confirm_newpwd_type: 'password',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    // this.start();
  },
  methods: {
    show_passwd() {
      if (this.oldpwd_type == 'password') {
        this.oldpwd_type = 'text';
      } else {
        this.oldpwd_type = 'password';
      }
    },
    show_npasswd() {
      if (this.newpwd_type == 'password') {
        this.newpwd_type = 'text';
      } else {
        this.newpwd_type = 'password';
      }
    },
    show_snpasswd() {
      if (this.confirm_newpwd_type == 'password') {
        this.confirm_newpwd_type = 'text';
      } else {
        this.confirm_newpwd_type = 'password';
      }
    },
    handleSubmit() {
      if (!this.passwd) {
        this.$notify(this.$t('lang.input_pw'));
        return;
      }

      if (!this.npasswd) {
        this.$notify(this.$t('lang.input_new_pay_pw'));
        return;
      }

      if (!this.snpasswd) {
        this.$notify(this.$t('lang.input_confirm_pay_pw'));
        return;
      }

      if (this.npasswd !== this.snpasswd) {
        this.$notify(this.$t('lang.not_match_pw'));
        return;
      }

      Fetch('/user/info/resetpaypwd', {
        passwd: this.passwd,
        npasswd: this.npasswd,
      }).then(() => {
        this.$notify({
          background: '#07c160',
          message: this.$t('lang.find_success'),
        });
        this.$router.back();
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/set.less';
</style>
