<template>
  <div class="container">
    <div class="lucky-wheel">
      <div class="lucky-title">
        <img src="./image/top.png" />
      </div>
      <div class="wheel-main">
        <div class="wheel-pointer-box">
          <div
            class="wheel-pointer"
            @click="rotate_handle()"
            :style="{
              transform: rotate_angle_pointer,
              transition: rotate_transition_pointer,
            }"
          ></div>
        </div>
        <div
          class="wheel-bg"
          :style="{
            transform: rotate_angle,
            transition: rotate_transition,
            'background-image': background,
          }"
        >
          <div class="prize-list"></div>
        </div>
      </div>
    </div>
    <div class="main">
      <div class="content">
        {{ $t('lang.left_times') }} {{ lottery_ticket }}
      </div>
      <div class="remark" v-html="prize_remark"></div>
      <div class="jl">
        <p>
          <router-link to="/" class="zjjl"
            >{{ $t('lang.gohome') }}&gt;&gt;</router-link
          >
          <router-link to="/kefu" class="zjjl"
            >{{ $t('lang.online_customer') }}&gt;&gt;</router-link
          >
          <span @click="dialog">{{ $t('lang.rules') }}&gt;&gt;</span>
        </p>
      </div>
      <div class="gdbox">
        <strong>{{ $t('lang.winlogs') }}</strong>
        <p class="tit">
          <span>{{ $t('lang.mobile') }}</span>
          <span>{{ $t('lang.getwin') }}</span>
        </p>
        <div class="zhongj-bbk">
          <div class="zhongj-bb" style="overflow:hidden;" ref="outbox">
            <div class="zhongj-bbl inner-container" ref="movebox">
              <div class="prize_list" v-for="(item, key) in data" :key="key">
                <li>{{ item.mobile }}</li>
                <li>{{ $t('lang.getgifts') }}{{ item.jp_name }}</li>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="toast" v-show="toast_control">
      <div class="toast-container">
        <img :src="prize_img" class="toast-picture" />
        <div class="close" @click="toast_control = false"></div>
        <div class="toast-title">
          {{ prize_name }}
        </div>
        <div class="toast-btn">
          <div class="toast-cancel" @click="toast_control = false">
            {{ $t('lang.close') }}
          </div>
        </div>
      </div>
    </div>
    <div class="toast-mask" v-show="toast_control"></div>
  </div>
</template>
<script>
import { Dialog } from 'vant';
import Fetch from '../../utils/fetch';

export default {
  components: {
    [Dialog.Component.name]: Dialog.Component,
  },
  data() {
    return {
      easejoy_bean: 0, //
      lottery_ticket: 0, //
      toast_control: true, //
      hasPrize: false, //
      start_rotating_degree: 0, //
      rotate_angle: 0, //
      start_rotating_degree_pointer: 0, //
      rotate_angle_pointer: 0, //
      rotate_transition: 'transform 3s ease-in-out', //
      rotate_transition_pointer: 'transform 3s ease-in-out', //
      click_flag: true, //
      prize_name: this.$t('lang.not_win'),
      prize_img: require('./image/sorry.png'),
      loading: false,
      data: [],
      isShow: true,
      prize_num: 0,
      background: '',
      prize_remark: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    Fetch('/user/prize/index').then(res => {
      this.prize_remark = res.data.prize_remark;
      this.data = res.data.prize_list;
      this.lottery_ticket = res.data.prize_num;
      this.background = 'url("' + res.data.background + '")';
      this.gd();
    });
  },
  computed: {},
  methods: {
    //
    rotate_handle() {
      if (this.lottery_ticket <= 0) {
        this.$notify(this.$t('lang.no_times'));
        return;
      }

      if (!this.loading) {
        this.loading = true;
        Fetch('/user/prize/apply')
          .then(res => {
            this.index = res.data.index; //
            this.prize_name = res.data.name;
            if (res.data.is_prize) {
              this.prize_img = require('./image/congratulation.png');
            } else {
              this.prize_img = require('./image/sorry.png');
            }
            this.lottery_ticket -= 1;
            this.rotating();
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
    rotating() {
      if (!this.click_flag) return;
      var during_time = 1; // 1s
      var result_index = this.index; // prize_list
      var result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5]; //
      var rand_circle = 7; // 2-3
      this.click_flag = false; // 
      // 
      var rotate_angle =
        this.start_rotating_degree +
        rand_circle * 360 +
        result_angle[result_index] -
        (this.start_rotating_degree % 360);
      this.start_rotating_degree = rotate_angle;
      this.rotate_angle = 'rotate(' + rotate_angle + 'deg)';
      // //
      // this.rotate_angle_pointer = "rotate("+this.start_rotating_degree_pointer + 360*rand_circle+"deg)";
      // this.start_rotating_degree_pointer =360*rand_circle;
      var that = this;
      // 
      setTimeout(function() {
        that.click_flag = true;
        that.game_over();
      }, during_time * 3000); // 
    },
    game_over() {
      this.toast_control = true;
      this.loading = false;
    },
    //
    close_toast() {
      this.toast_control = false;
    },
    dialog() {
      Dialog.alert({
        title: this.$t('lang.rules_des'),
        message: this.$t('lang.rules_content'),
        className: 'cj_dialog',
      }).then(() => {
        console.log('dialog closed.');
      });
    },
    gd() {
      var moveTarget = this.$refs.movebox;
      var outbox = this.$refs.outbox;
      if (this.data.length <= 6) {
        return;
      }

      var initTop = 0;
      setInterval(function() {
        initTop++;
        if (initTop >= moveTarget.scrollHeight / 2) {
          initTop = 0;
        }

        moveTarget.style = 'transform: translateY(-' + initTop + 'px)';
      }, 35);
    },
  },
};
</script>
<style lang="less">
