<template>
  <div class="bg">
    <div class="header">
      <div class="head">
        <router-link to="/jsq" class="back"></router-link>
        {{ $t('lang.cal_result') }}
      </div>
    </div>
    <div class="mt1">
      <table class="table">
        <thead>
          <tr>
            <th>{{ $t('lang.income_date') }}</th>
            <th>{{ $t('lang.income_num') }}</th>
            <th>{{ $t('lang.back_money') }}</th>
            <th>{{ $t('lang.back_apr') }}</th>
            <th>{{ $t('lang.release_money') }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in data.list" :key="key">
            <td>{{ item.date }}</td>
            <td>{{ item.smoney }}</td>
            <td>{{ item.money }}</td>
            <td>{{ item.apr_money }}</td>
            <td>{{ item.sy_money }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="mt1 mt2">
      <table class="table">
        <thead>
          <tr>
            <th>{{ $t('lang.summary') }}</th>
            <th>{{ data.sum.smoney }}</th>
            <th>{{ data.sum.money }}</th>
            <th>{{ data.sum.apr_money }}</th>
            <th>0</th>
          </tr>
        </thead>
      </table>
    </div>
    <div class="mt3">
      <div class="mt3_info">{{ $t('lang.act_fund') }}</div>
      <div class="mt3_money">
         {{ data.sum.apr_money }}{{ $t('lang.rmb') }}
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  data() {
    return {
      data: {
        sum: {},
      },
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/item/dq/cal', {
        money: this.$route.query.money,
        days: this.$route.query.days,
        type: this.$route.query.type,
        apr: this.$route.query.apr,
      }).then(res => {
        this.data = res.data;
      });
    },
  },
};
</script>

