<template>
  <div class="hz_wrap">
    <div class="header">
      <div class="head">
        {{ $t('lang.member_zhuan') }}

        <router-link to="/user" class="back"></router-link>
      </div>
    </div>
    <form method="post" class="form" @submit.prevent="handleSubmit">
      <div class="top">
        <div class="left"></div>
        <input
          type="number"
          class="mobile"
          name="phone"
          v-model.trim="mobile"
          :placeholder="$t('lang.input_to_mobile')"
        />
      </div>

      <div class="money_box">
        <div class="title1">{{ $t('lang.transfer_money') }}</div>
        <div class="amount_box">
          <span></span>
          <input
            class="inp inp_money"
            type="number"
            v-model.trim="money"
            name="money"
            :placeholder="$t('lang.input_transfer_money')"
          />
          <div
            class="clear_num"
            v-show="money > 0"
            @click="clear_num_handle"
          ></div>
        </div>
        <div class="line"></div>
        <div class="remian_money">
          {{ $t('lang.access_transfer_money') }}<span>{{ data.money }}</span>
        </div>
      </div>

      <div class="pay_box">
        <span class="left_icon"></span>
        <input
          :type="psd_type"
          class="inp_pay"
          v-model.trim="passwd"
          :placeholder="$t('lang.input_sele_pw')"
        />
        <span
          class="right_icon"
          :class="psd_type == 'password' ? '' : 'right_icon_open'"
          @click="show_psd"
        ></span>
      </div>
      <div class="forgetpay">
        <div class="btn" @click="$router.push('/resetpaypwd')">
          {{ $t('lang.fotget_pw') }} ?
        </div>
      </div>
      <button
        type="submit"
        class="sbtn basic_btn_v2"
        :class="money > 0 && passwd != '' ? '' : 'no_touch'"
      >
        {{ $t('lang.confirm_transfer') }}
      </button>
    </form>
    <transition name="fade">
      <div class="mask" v-show="zz_success">
        <div class="rz_inner">
          <img src="./images/chenggong_2.png" alt="" />
          <p>{{ $t('lang.transfer_success') }}</p>
          <div class="basic_btn_v2" @click="zz_success_handle">
            {{ $t('lang.confirm') }}
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      money: '',
      mobile: '',
      passwd: '',
      psd_type: 'password',
      zz_success: false,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/huzhuan/index').then(r => {
        this.data = r.data;
      });
    },
    clear_num_handle() {
      this.money = '';
    },
    zz_success_handle() {
      this.zz_success = false;
      this.$router.push('/zhuanzhang');
    },
    show_psd() {
      if (this.psd_type == 'password') {
        this.psd_type = 'text';
      } else {
        this.psd_type = 'password';
      }
    },
    getClass(channel) {
      return 'recharge_online_' + channel;
    },
    handleSubmit() {
      if (!this.mobile) {
        this.$notify({
          message: this.$t('lang.input_to_mobile'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.money) {
        this.$notify({
          message: this.$t('lang.input_transfer_money'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.passwd) {
        this.$notify({
          message: this.$t('lang.input_sale_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/user/huzhuan/apply', {
        passwd: this.passwd,
        money: this.money,
        mobile: this.mobile,
      }).then(() => {
        this.zz_success = true;
      });
    },
  },
};
</script>

<style lang="less" scoped>
html,
body {
  background: #f3f4f4;
}
.hz_wrap {
  background-color: #f7f8f9;
  min-height: 100%;
}

.form {
  margin: 0px auto 0 auto;
  position: relative;
}

.form .top {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  background: #ffffff;
  height: 49px;
  line-height: 49px;
  margin-top: 8px;
  padding: 0 18px;
}

.form .top .mobile {
  width: 100%;
  height: 49px;
  background-color: #ffffff;
  color: #5d5e60;
  padding: 13px 8px;
  font-size: 15px;
}

.form .top .left {
  height: 24px;
  width: 25px;
  padding-right: 2px;
  background-color: #ffffff;
  color: #5d5e60;
  background: url(images/zhuanzhang.png) no-repeat center center;
  background-size: 100% auto;
}

.money_box {
  padding: 18px 17px 0 17px;
  background-color: #ffffff;
  margin-top: 13px;
}

.money_box .title1 {
  color: #333333;
  font-size: 16px;
}

.money_box .amount_box {
  display: flex;
  height: 45px;
  line-height: 45px;
  font-size: 28px;
  align-items: center;
  position: relative;
  margin: 23px auto 8px auto;
  .clear_num {
    width: 13px;
    height: 13px;
    position: absolute;
    background: url(./images/clear_num.png) no-repeat center center;
    background-size: 100% 100%;
    top: 18px;
    right: 0;
  }
}

.money_box .amount_box > span {
  color: #333333;
}

.money_box .amount_box > input {
  color: #333333;
  flex: 1;
  font-size: 28px;
  margin-left: 8px;
  overflow: hidden;
}

.money_box .remian_money {
  display: inline-block;
  height: 18px;
  line-height: 18px;
  color: #999999;
  font-size: 13px;
  margin: 13px auto;
}

.money_box .remian_money > span {
  font-size: 14px;
}

.money_box .remian_money > span {
  font-size: 14px;
}

.money_box .line {
  width: 100%;
  height: 1px;
  background: #dcdcdc;
}

.pay_box {
  width: 100%;
  background-color: #ffffff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  height: 49px;
  padding: 0 17px;
  margin: 8px auto;
}

.form .pay_box .left_icon {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 18px;
  flex: 0 0 18px;
  height: 20px;
  background-image: url(./images/mima.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.forgetpay {
  padding: 0px 16px;
  margin: 9px 0;
  height: 18px;
  line-height: 18px;
  .btn {
    float: right;
    font-size: 14px;
    color: #999999;
  }
}
.form .pay_box .right_icon {
  width: 21px;
  height: 14px;
  background-image: url(./images/biyan.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.form .pay_box .right_icon.right_icon_open {
  background-image: url(./images/zhengyan.png);
}

.form .pay_box .inp_pay {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  height: 29px;
  line-height: 29px;
  font-size: 14px;
  line-height: 14px;
  color: #212121;
  margin-left: 6px;
}

.form .sbtn {
  display: block;
  width: 90.1%;
  height: 47px;
  line-height: 47px;
  text-align: center;
  margin-top: 21px;
}
.rz_inner {
  width: 77.6%;
  padding: 39px 60px 26px 60px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 80px;
  }
  p {
    font-size: 17px;
    color: #000;
    line-height: 24px;
    margin: 32px auto 39px auto;
  }
  div {
    height: 40px;
    line-height: 40px;
    width: 100%;
  }
}
</style>
