<template>
  <div class="detail-page">
    <div class="headr-top">
      <van-nav-bar
        :title="$t('lang.exchange_product')"
        left-arrow
        class="header-nav"
        fixed
        @click-left="$router.back()"
      />
    </div>
    <div class="goods-head-img">
      <van-swipe
        class="my-swipe"
        :autoplay="4000"
        indicator-color="white"
        @change="onChange"
      >
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img v-lazy="$img + image" />
        </van-swipe-item>
        <div ref="dian" class="custom-indicator" slot="indicator">
          <div
            v-for="(img, x) in images"
            :key="x"
            :class="x === 0 ? 'selected' : ''"
          ></div>
        </div>
      </van-swipe>
      <!-- <div class="pic-type">
        <span><Icon name="play" style="line-height: 13px;" /></span>
        <span class="is-active"></span>
      </div> -->
    </div>
    <div class="goods-title">
      <div>{{ goodsTitle }}</div>
      <div class="goods_img">
        <img src="./images/pro.png" alt="" />
        <span>{{ credit }}</span>
        <!-- {{ $t('lang.integral') }} -->
      </div>
    </div>
    <div class="goods-con-wrap">
      <div class="goods-con-title">
        <div>{{ $t('lang.product_des') }}</div>
      </div>
      <p class="goods-con-detail" v-html="goodsContent" />
    </div>
    <div class="button-wrap">
      <div class="button-btn" @click="onClick">
        {{ $t('lang.exchange_rightnow') }}
      </div>
    </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Icon, Dialog } from 'vant';
export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem,
  },
  data() {
    return {
      images: [],
      current: 1,
      maxSwipe: 1,
      goodsTitle: '',
      goodsContent: '',
      id: 1,
      credit: '',
    };
  },
  created() {
    this.$parent.footer(false, 'index');
    this.id = this.$route.query.id;
    if (!this.id) this.$router.back({ query: { id: this.id } });
    this.getInfo(this.id);
  },
  methods: {
    onChange(index) {
      this.current = index + 1;
    },
    onClick() {
      this.$router.push({ name: 'point_confirm', query: { id: this.id } });
    },
    getInfo(id) {
      this.$Fetch('/goods/info/view', { id }).then(res => {
        console.log('res :>> ', res);
        const data = res.data.view;
        if (data.status !== 'Y') {
          // this.$Dialog.alert({ message: '' }).then(() => {
          this.$Dialog
            .alert({ message: this.$t('lang.sell_null') })
            .then(() => {
              this.$router.back();
            });
        }
        this.images = data.thumbs;
        this.maxSwipe = data.thumbs.length;
        this.goodsTitle = data.title;
        this.credit = data.credit;
        this.goodsContent = data.content || this.$t('lang.no_product_des');
      });
    },
  },
};
</script>

