<template>
  <div class="fund_box">
    <div class="header">
      <div class="head">
        <router-link to="/user" class="back"></router-link
        >{{ $t('lang.amount_list') }}
      </div>
    </div>
    <div class="fund_wrap">
      <div class="money_title">
        <span class="sanjiao" @click="date_show = true"
          >{{ now_year }}.{{ now_month }}</span
        >
        <span class="sanjiao" @click="type_show = true"
          >{{ $t('lang.choose_sale_type_in') }}
        </span>
      </div>
      <div class="search_box">
        <div class="month_box">
          <div class="search_tit">
            <div class="month_day">
              {{ sliceNum(now_month) }}
              <i></i>
            </div>
            <div class="money_detail">
              <!-- <p>  {{pay_money}}</p> -->
              <!-- <p>  {{income_money}}</p> -->
            </div>
          </div>
          <div class="money_list">
            <div class="list_box" v-for="(item, key) in data" :key="key">
              <div class="money_icon">
                <img src="./images/jifen.png" alt />
              </div>
              <div class="sum">
                <p class="sum_tit">{{ item.title }}</p>
                <p class="sum_type">{{ item.stype_name }}</p>
                <p class="sum_time">{{ item.addtime_date }}</p>
                <div class="sum_num">{{ item.money }}</div>
                <div
                  class="sum_status"
                  :class="
                    item.status == 'D' ||
                    item.status == 'S' ||
                    item.status == 'N'
                      ? 'red_status'
                      : ''
                  "
                >
                  {{ item.status_name }}
                </div>
              </div>
            </div>
            <div class="no_list" v-if="data.length == 0">
              {{ now_year }}{{ $t('lang.year') }} {{ now_month
              }}{{ $t('lang.m_no_data') }}
              ~
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <TypeMask :show="type_show" current="point" />

    <!--  -->
    <transition name="slide-fade">
      <div class="mask" v-show="date_show">
        <van-datetime-picker
          v-model="currentDate"
          type="year-month"
          :title="$t('lang.choose_ym')"
          confirm-button-text="Quyt nh"
          cancel-button-text="Hu b"
          :min-date="minDate"
          :max-date="maxDate"
          :formatter="formatter"
          @confirm="choose_date"
          @cancel="date_show = false"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import Vue from 'vue';
import { DatetimePicker } from 'vant';
import Fetch from '../../utils/fetch';
import TypeMask from './components/TypeMask';
Vue.use(DatetimePicker);
export default {
  name: 'index',
  components: { TypeMask },
  data() {
    return {
      data: {},
      search_data: {},
      now_type: 0,
      date_show: false,
      type_show: false,
      now_month: 0,
      now_year: new Date().getFullYear(),
      pay_money: 0,
      income_money: 0,
      minDate: new Date(),
      maxDate: new Date(),
      currentDate: new Date(),
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    var n_day = new Date();
    var d_year = n_day.getFullYear();
    var d_month =
      n_day.getMonth() + 1 < 10
        ? '0' + (n_day.getMonth() + 1)
        : n_day.getMonth() + 1;
    var d_day = n_day.getDate() < 10 ? '0' + n_day.getDate() : n_day.getDate();
    this.now_month = d_month;
    this.minDate = new Date(d_year, 0, 1);
    this.maxDate = new Date(d_year, d_month - 1, 1);
    var str = d_year + '-' + d_month;
    this.start(str);
  },
  methods: {
    formatter(type, val) {
      if (type === 'year') {
        // return `${val}`;
        return val + this.$t('lang.year');
      } else if (type === 'month') {
        // return `${val}`;
        return val + this.$t('lang.month');
      }
      return val;
    },
    start(str_day) {
      // 
      var that = this;
      Fetch('/user/funds/search', {
        month: str_day,
        type: 'credit',
      }).then(res => {
        this.data = res.data.list;
        var pay = 0;
        var income = 0;

        this.data.forEach(function(k, v) {
          if (k.status == 'Y' && k.money > 0) {
            income += parseFloat(k.money);
          }
        });
        this.pay_money = pay.toFixed(2);
        this.income_money = income.toFixed(2);
      });
    },
    choose_date(v) {
      this.date_show = false;
      this.now_year = v.getFullYear();
      this.now_month =
        v.getMonth() + 1 < 10 ? '0' + (v.getMonth() + 1) : v.getMonth() + 1;
      this.currentDate = new Date(this.now_year, this.now_month - 1, 1);
      this.start(this.now_year + '-' + this.now_month);
    },
    sliceNum(str) {
      if (~~str < 10) {
        return str.toString().slice(1, 2);
      } else {
        return str;
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/money.less';
.fund_wrap .search_box .money_list .list_box {
  height: auto;
  .sum {
    border: none;
  }
}
</style>
