<template>
  <div class="app">
    <div class="app_top">
      <div class="top_left">
        <img
          src="data:image/png;base64,"
        />
        <div class="left1">{{ $t('lang.start_time') }}</div>
        <div class="left2">{{ data.date }}</div>
      </div>
      <div class="top_center">
        <img
          src="data:image/png;base64,"
        />
        <div class="center1">{{ $t('lang.remaining_num') }}</div>
        <div class="center2">{{ data.num }}</div>
      </div>
      <div class="top_right">
        <div class="right" @click="tokefu">
          {{ $t('lang.contact_customer') }}
        </div>
      </div>
    </div>
    <div class="content">
      <vue-editor v-model="data.content" style="display: none" />
      <div
        class="contract_box ql-editor "
        style="width: 100%"
        v-html="data.content"
      ></div>
    </div>
    <div class="bottom">
      <router-link to="/app_reg">
        <div class="a1">
          <span class="b1">{{ $t('lang.join_now') }}</span>
          <span class="b2"
            ><van-count-down :time="time" format="mm:ss" @finish="call"
          /></span>
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import { CountDown } from 'vant';
import Fetch from '../../utils/fetch';
import { Dialog } from 'vant';
import { VueEditor } from 'vue2-editor';

Vue.use(CountDown);

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      data: '',
      time: 60 * 1000,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    Fetch('/api/api/webconfig', { type: 'tg' }).then(res => {
      this.data = res.data;
      this.time = parseInt(res.data.time) * 1000;
    });
  },
  methods: {
    tokefu() {
      this.$router.push('/kefu');
    },
    call() {
      Dialog.confirm({
        title: this.$t('lang.less_num'),
        message: this.$t('lang.art_title'),
        confirmButtonText: this.$t('lang.register_now'),
        confirmButtonColor: 'rgb(251, 171, 0)',
        cancelButtonText: this.$t('lang.more_think'),
      }).then(() => {
        this.$router.push('/app_reg');
      });
    },
  },
};
</script>

<style lang="less">
.app {
  /deep/ .ql-editor {
    padding: 0;
  }
  .app_top {
    background-color: rgb(255, 225, 15);
    position: fixed;
    height: 13.33333vw;
    width: 100%;
    z-index: 2;
    font-size: 3.46667vw;
    color: #000;

    .top_left {
      display: inline-block;
      position: relative;
      width: 37%;

      > img {
        position: absolute;
        width: 3.46667vw;
        height: 3.46667vw;
        left: 4vw;
        top: 2.66667vw;
      }

      .left1 {
        font-weight: 700;
        margin-left: 8.53333vw;
        margin-top: 2.66667vw;
      }

      .left2 {
        margin-left: 8.53333vw;
        margin-top: 1.33333vw;
      }
    }

    .top_center {
      display: inline-block;
      position: relative;
      width: 33.33%;

      > img {
        position: absolute;
        width: 3.46667vw;
        height: 3.46667vw;
        left: 4vw;
        top: 2.66667vw;
      }

      .center1 {
        font-weight: 700;
        margin-left: 8.53333vw;
        margin-top: 2.66667vw;
      }

      .center2 {
        margin-left: 8.53333vw;
        margin-top: 1.33333vw;
      }
    }

    .top_right {
      position: absolute;
      height: 13.33333vw;
      width: 28%;
      right: 0;
      top: 0;

      .right {
        font-weight: 700;
        width: 20.53333vw;
        height: 7.46667vw;
        line-height: 7.46667vw;
        text-indent: 2.66667vw;
        border: 1px solid #000;
        border-radius: 1.06667vw;
        margin-top: 2.93333vw;
        position: absolute;
        right: 4vw;
      }
    }
  }

  .bottom {
    position: fixed;
    width: 100%;
    height: 16vw;
    bottom: 0;
    z-index: 2;
    background-color: #333;

    a {
      background: transparent;
      text-decoration: none;
    }

    .a1 {
      width: 80%;
      height: 10.66667vw;
      line-height: 10.66667vw;
      top: 2.66667vw;
      left: 10%;
      text-align: center;
      font-size: 5.33333vw;
      color: #333;
      background: #ffe10f;
      -webkit-box-shadow: 0.53333vw 1.06667vw 2.13333vw 0 rgba(0, 0, 0, 0.04);
      box-shadow: 0.53333vw 1.06667vw 2.13333vw 0 rgba(0, 0, 0, 0.04);
      border-radius: 12vw;
      position: absolute;
      z-index: 3;

      .b1 {
        font-weight: 700;
        font-size: 5.06667vw;
      }

      .b2 {
        font-size: 3.73333vw;
        position: absolute;
        right: 6.66667vw;
        /deep/ .van-count-down {
          padding-top: 14px;
          font-size: 16px;
        }
      }
    }
  }

  .content {
    background-color: #fff;
    position: relative;
    z-index: 1;
    left: 0;
    width: 100%;
    padding-top: 13.33333vw;
  }
}
</style>
