<template>
  <div class="container">
    <div class="header">
      <div class="head">
        <router-link to="/recharge" class="back"></router-link>
        {{ $t('lang.bank_deposit') }}
      </div>
    </div>
    <div class="money">
      <span>{{ $t('lang.bank_entry') }} </span>
      <span class="inp"
        >{{ money }}<span>{{ $t('lang.rmb') }} </span></span
      >
    </div>
    <div class="infos">
      <div class="van-hairline--bottom">
        <span class="before_font">{{ $t('lang.receiving_bank') }} </span>
        <span class="before_font_c2">{{ bank_name }}</span>
      </div>
      <div class="van-hairline--bottom">
        <span class="before_font">{{ $t('lang.payee') }} </span>
        <span class="before_font_c2">{{ bank_user }}</span>
        <button
          v-clipboard="() => bank_user"
          v-clipboard:success="copy"
          class="copy"
        >
          {{ $t('lang.copy') }}
        </button>
      </div>
      <div>
        <span class="before_font">{{ $t('lang.payee_card') }} </span>
        <span class="before_font_c2">{{ bank_card }}</span>
        <button
          v-clipboard="() => bank_card"
          v-clipboard:success="copy"
          class="copy"
        >
          {{ $t('lang.copy') }}
        </button>
      </div>
    </div>
    <div class="infos">
      <label class="van-hairline--bottom"
        ><span class="before_font1">{{ $t('lang.payer') }} </span
        ><input
          type="text"
          class="left_line"
          v-model.trim="pay_user"
          :placeholder="$t('lang.input_payer')"
      /></label>
      <label class="van-hairline--bottom"
        ><span class="before_font1">{{ $t('lang.paynotetag') }} </span
        ><input
          type="text"
          v-model.trim="pay_remark"
          :placeholder="$t('lang.input_paynote')"
      /></label>
    </div>
    <div
      class="basic_btn_v2"
      :class="pay_user != '' ? '' : 'no_touch'"
      @click="submit"
    >
      {{ $t('lang.submit') }}
    </div>
    <p v-if="this.account != ''">
      {{ this.account }}
    </p>
    <p v-else>
      {{ $t('lang.paynote') }}
    </p>
  </div>
</template>
<script>
import Vue from 'vue';
import { NavBar, Button } from 'vant';
import Fetch from '../../utils/fetch';
import Clipboard from 'v-clipboard';

Vue.use(NavBar)
  .use(Button)
  .use(Clipboard);
export default {
  data() {
    return {
      bank_name: '',
      bank_user: '',
      bank_card: '',
      pay_user: '',
      pay_remark: '',
      money: 0,
      account: '',
    };
  },
  mounted() {
    this.money = this.$route.query.money;
    this.getData();
  },
  methods: {
    onClickLeft() {
      this.$router.replace('/recharge');
    },
    copy() {
      this.$notify({
        background: '#EAF1FF',
        color: '#3292FF',
        message: this.$t('lang.copy_success'),
      });
    },
    submit() {
      Fetch('/user/invest/applyb', {
        money: this.money,
        remark: this.pay_remark,
        name: this.pay_user,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.submit_s'),
        });
        this.$router.push('/user');
      });
    },
    getData() {
      Fetch('/user/invest/bank').then(res => {
        this.bank_user = res.data.bank_user;
        this.bank_card = res.data.bank_card;
        this.bank_name = res.data.bank_name;
        this.account = res.data.account;
      });
    },
  },
};
</script>
<style lang="less" scoped>
.container {
  min-height: 100%;
  background-color: #f7f8f9;

  .money {
    background-color: #ffffff;
    height: 40px;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    margin-top: 8px;
    padding: 0 18px;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    .inp {
      margin-left: 19px;
      > span {
        font-size: 14px;
        display: inline-block;
      }
    }
  }

  .infos {
    background-color: #ffffff;
    font-size: 14px;
    margin-top: 13px;
    > div,
    label {
      display: flex;
      padding: 0 18px;
      width: 100%;
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid #e3e4e5;
      justify-content: flex-start;
      align-items: center;
      span.before_font {
        color: #b1b1b1;
        width: 100px;
        font-weight: normal;
      }
      span.before_font1 {
        color: #b1b1b1;
        width: 200px;
        font-weight: normal;
      }
      input {
        padding-left: 7px;
        margin-left: 7px;
        height: 30px;
        width: 100%;
      }
    }
    span:last-of-type {
      color: #828282;
      font-weight: bold;
    }
    > div:last-of-type {
      border-bottom: none;
    }

    .copy {
      color: #00b389;
      margin-left: auto;
      font-size: 13px;
      white-space: nowrap;
    }
  }

  > p {
    font-size: 13px;
    line-height: 18px;
    margin: 0 18px;
    color: #b1b1b1;
  }
}
.basic_btn_v2 {
  height: 47px;
  line-height: 47px;
  width: 90.9%;
  margin: 64px auto 26px auto;
}
.before_font_c2 {
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 0.2rem;
}
</style>
