<script>
import { NavBar, List, Cell } from 'vant';
import point from '../index/component/point';
export default {
  components: {
    [NavBar.name]: NavBar,
    [List.name]: List,
    [Cell.name]: Cell,
    point,
  },
  data() {
    return {
      active: '',
      classify: [],
      list: [],
      point_list: [],
      i: 0,
      pageNum: 1,
      loading: false,
      isShop: 'off',
      finished: false,
      focus_list: [],
      jx: true,
      tj: false,
      xs: false,
      cg: false,
    };
  },
  watch: {
    active() {
      this.getList();
    },
  },
  created() {
    this.$parent.footer(true, 'focus');
    this.getConfig();
  },
  mounted() {
    this.getFocus();
    this.shopOpen();
  },
  methods: {
    jxBtn() {
      console.log(11);
      this.jx = true;
      this.tj = false;
      this.xs = false;
      this.cg = false;
    },
    tjBtn() {
      this.tj = true;
      this.jx = false;
      this.xs = false;
      this.cg = false;
    },
    xsBtn() {
      this.xs = true;
      this.jx = false;
      this.tj = false;
      this.cg = false;
    },
    cgBtn() {
      this.cg = true;
      this.tj = false;
      this.jx = false;
      this.xs = false;
    },

    shopOpen() {
      this.$Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.isShop = res.data.store_switch;
        if (this.isShop === 'on') {
          this.getPointList();
        }
      });
    },
    getFocus() {
      this.$Fetch('/article/about/search', { type: 'focus' }).then(res => {
        this.focus_list = res.data.list;
      });
    },
    GoDetail(id) {
      console.log('id :>> ', id);
      this.$router.push({ name: 'point_detail', query: { id } });
    },
    onMore() {
      this.$router.push({ name: 'point_mall' });
    },
    getPointList() {
      this.$Fetch('/goods/info/search', {
        order: 'addtime desc',
      }).then(res => {
        this.point_list = res.data.list.slice(0, 4) || [];
      });
    },
    getConfig() {
      // 
      this.loading = true;
      this.$Fetch('/goods/info/config').then(res => {
        this.classify = res.data.cats;
      });
    },
    getList() {
      if (cat_id === '') return;
      this.loading = true;
      const cat_id = this.classify[this.active].id;
      this.list = [];
      return this.$Fetch('/goods/info/search', { cat_id }, {}, false).then(
        res => {
          this.loading = false;
          this.finished = true;
          this.list = this.list.concat(res.data.list);
        }
      );
    },
    onClick(item) {
      this.$router.push({ name: 'point_detail', query: { id: item.id } });
    },
    onSwipeLeft(e) {
      if (this.active === this.classify.length - 1) return;
      this.active++;
    },
    onSwipeRight(e) {
      e.preventDefault();
      if (this.active === 0) return;
      this.active--;
    },
  },
};
</script>

