      @click-right="rule = false"
      @click-left="$router.push('/team')"
    />
    <!-- :right-text="$t('lang.s_rules')" -->
    <!-- <div class="top" @click="showShare"> -->
    <!-- <div class="top"></div> -->

    <div class="con">
      <div class="con_1" v-for="item in data.teamlist" :key="item.id">
        <!-- <div class="biaozhi">
          <p></p>
        </div> -->
        <img :src="'/img/dengji' + item.id + '.png'" alt="" />
        <div class="detail">
          <p>{{ item.name }}</p>
          <span>{{ item.content }}</span>
        </div>
