<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Dialog } from 'vant';

// 
Vue.use(Dialog);

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      rule: false,
      is_show_share: false,
      data_length: false,
      rulefont: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
    this.$Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
      this.rulefont = res.data.invite_firends;
    });
  },
  methods: {
    close() {
      this.is_show_share = false;
    },
    showShare() {
      this.is_show_share = true;
    },
    start() {
      Fetch('/user/info/share').then(res => {
        this.data = res.data;
        if (res.data.share_user.length == 0) {
          this.data_length = true;
        }
      });
    },
    copy() {
      var clipboard = new this.Clipboard('#copy_text');
      clipboard.on('success', e => {
        this.$notify({
          message: 'copy success',
          color: '#3292FF',
          background: '#FFEAEA',
        });
        clipboard.destroy();
      });
      clipboard.on('error', e => {
        // 
        // Message({
        //   message: '',
        //   type: 'warning',
        // });
        // 
        clipboard.destroy();
      });
    },
    downImg() {
      if (!window.plus) return;
      window.plus.gallery.save(
        this.data.share_image_url,
        function() {
          // alert('!');
        },
        function() {
          // alert('!');
        }
      );
    },
    downs() {
      var alink = document.createElement('a');
      alink.href = this.data.share_image_url;
      alink.download = 'pic'; //
      alink.click();
    },
  }, // 
};
</script>

