<script>
import Fetch from '../../utils/fetch';
import ProgressBar from '../../components/progressbar';
export default {
  name: 'index',
  components: {
    ProgressBar,
  },
  data() {
    return {
      data: {},
      id: 0,
      details: false,
      style: {},
    };
  },
  created() {
    this.$parent.footer(false);
    this.$nextTick(() => {
      this._initBody();
    });
  },
  mounted() {
    this.id = this.$router.history.current.params.id;
    this.start();
  },
  methods: {
    start() {
      Fetch('/item/dq/view', { id: this.id }).then(res => {
        this.data = res.data.view;
      });
    },

    getApply() {
      return '/item/apply/' + this.id;
    },
    show_detail() {
      this.details = !this.details;
    },
    gotop() {
      let top = document.documentElement.scrollTop || document.body.scrollTop;
      const timeTop = setInterval(() => {
        document.body.scrollTop = document.documentElement.scrollTop = top -= 30;
        if (top <= 0) {
          clearInterval(timeTop);
        }
      }, 10);
    },
    _initBody() {
      const vm = this;
      this.fn = function scorllEvent() {
        vm.opacity = window.pageYOffset / 250;
        vm.style = { background: `rgba(0,179,137,${vm.opacity})` };
      };
      window.addEventListener('scroll', this.fn, false);
    },
    offScrollEvent() {
      window.removeEventListener('scroll', this.fn, false);
    },

    getTop() {
      $('html,body').animate(
        {
          scrollTop: '0px',
        },
        300
      );
    },
  },
};
</script>

