<template>
  <div class="fund_box">
    <div class="header">
      <div class="head">
        <router-link to="/user" class="back"></router-link>
        {{ $t('lang.info_manage') }}
      </div>
    </div>
    <!-- <div class="fund_wrap">
            <table class="table">
                <tbody>
                <tr class="header">
                    <th class="first"></th>
                    <th></th>
                    <th></th>
                    <th class="last"></th>
                </tr>
                <tr v-for="(v,k) in data.list" :key="k">
                    <td v-if="!v.is_read" style="color: #00C800"></td>
                    <td v-if="v.is_read" style="color: #cccccc"></td>
                    <td>{{v.name}}</td>
                    <td>{{v.addtime|dateFormat}}</td>
                    <td>
                        <router-link class="button" :to="'/notice/' + v.cid"></router-link>
                    </td>
                </tr>
                </tbody>
            </table>
        </div> -->
    <div class="fund_wrap">
      <div
        class="fund_list"
        v-for="(v, k) in data.list"
        :key="k"
        @click="read_msg(v.cid)"
      >
        <img class="icon" src="./images/notice.png" alt="" />
        <h3>{{ v.name }}</h3>
        <div v-if="!v.is_read" class="read_status" style="color: #2D73FF">
          {{ $t('lang.no_read') }}
        </div>
        <div v-if="v.is_read" class="read_status" style="color: #999999">
          {{ $t('lang.has_read') }}
        </div>
        <div class="fund_content">
          <div class="van-ellipsis min_content" v-html="v.content"></div>
          <div class="add_time">{{ v.addtime | dateFormat }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import moment from 'moment';

export default {
  name: 'login',
  data() {
    return {
      data: [],
    };
  },
  created() {
    this.$parent.footer(false);
  },
  filters: {
    dateFormat(time) {
      return moment(parseInt(time) * 1000).format('MM.DD hh:mm');
    },
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/noticeread/search').then(r => {
        this.data = r.data;
      });

      console.log(this.data);
    },
    read_msg(id) {
      this.$router.push('/notice/' + id);
    },
  },
};
</script>

<style lang="less" scoped>
.fund_box {
  width: 100%;
  min-height: 100vh;
  background-color: #ffffff;
}
.fund_list {
  width: 100%;
  padding: 12px 18px 12px 49px;
  height: 58px;
  border-bottom: 1px solid #e3e4e5;
  position: relative;
  .icon {
    width: 26px;
    height: 26px;
    position: absolute;
    left: 11px;
    top: 16px;
  }
  h3 {
    height: 20px;
    line-height: 20px;
    font-size: 14px;
    color: #333333;
    font-weight: bold;
  }
  .fund_content {
    height: 18px;
    line-height: 18px;
    font-size: 13px;
    color: #8c8d91;
    margin-top: 2px;
    position: relative;
    .min_content {
      width: 76%;
      height: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
      position: relative;
    }
  }
  .read_status {
    position: absolute;
    top: 10px;
    line-height: 17px;
    font-size: 12px;
    right: 18px;
  }
  .add_time {
    position: absolute;
    line-height: 17px;
    color: #7d7d7f;
    top: 0px;
    right: 0px;
    font-size: 12px;
  }
}
</style>
