<template>
  <div class="content">
    <div class="header">
      <div class="head">
        <router-link to="/order" class="back"></router-link>
        {{ $t('lang.logistics_tracking') }}
      </div>
    </div>
    <div class="shop_detail">
      <div class="fruit_img">
        <img :src="image" alt="" />
      </div>
      <div class="ltd">
        <div class="item">
          <span class="tit">{{ $t('lang.logistics_status') }} </span>
          <span class="status oriange">{{
            data.state_name || $t('lang.no_logistics_status')
          }}</span>
        </div>
        <div class="item">
          <span class="tit">{{ $t('lang.logistics_company') }} </span>
          <span class="status gray">{{ data.kd_name }}</span>
        </div>
        <div class="item">
          <span class="tit">{{ $t('lang.logistics_no') }} </span>
          <span class="status gray">{{ data.kd_sn }}</span>
        </div>
        <div class="item">
          <span class="tit">{{ $t('lang.logistics_tel') }} </span>
          <span class="status blue"
            ><a :href="'tel:' + data.kd_tel">{{ data.kd_name }}</a></span
          >
        </div>
      </div>
    </div>
    <div class="wl_detail" v-if="load">
      <div class="title">{{ $t('lang.order_tracking') }}</div>
      <div class="no_wl" v-show="no_wl">{{ $t('lang.no_logistics') }} ~</div>
      <div class="step_box">
        <!-- <div class="step success active">
					<div class="line"></div>
					<h3></h3>
					<p>2016-07-12 12:40</p>
				</div>
				<div class="step success active">
					<div class="line"></div>
					<h3></h3>
					<p>2016-07-12 12:40</p>
				</div>
				<div class="step">
					<div class="line"></div>
					<h3></h3>
					<p>2016-07-12 12:40</p>
				</div>
				<div class="step ck">
					<div class="line"></div>
					<h3></h3>
					<p>2016-07-12 12:40</p>
				</div>
				<div class="step db">
					<div class="line"></div>
					<h3></h3>
					<p>2016-07-12 12:40</p>
				</div>
				<div class="step zf">
					<div class="line"></div>
					<h3></h3>
					<p>2016-07-12 12:40</p>
				</div> -->
        <div
          class="step"
          v-for="(activity, index) in data.kd_info"
          :key="index"
          :class="index == 0 && status ? 'success active' : 'ck'"
        >
          <div class="line"></div>
          <h3>{{ activity.context }}</h3>
          <p>{{ activity.time }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      load: false,
      data: {},
      id: 0,
      dh: '',
      no_wl: false,
      details: false,
      status: false,
      image: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.id = this.$router.history.current.params.id;
    this.dh = this.$router.history.current.params.dh;
    this.start();
    this.image = localStorage.getItem('imgs');
  },
  methods: {
    start() {
      Fetch('/goods/order/logisInfo', {
        id: this.id,
        courier_id: this.dh,
      }).then(res => {
        this.data = res.data;
        if (res.data.kd_info.length == 0) {
          this.no_wl = true;
        }
        this.load = true;
        if (res.data.state_name) {
          if (res.data.state_name.indexOf('') != -1) {
            this.status = true;
          }
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.content {
  width: 100%;
  min-height: 100%;
  background-color: #f7f8f9;
}

.shop_detail {
  width: 100%;
  background-color: #ffffff;
  padding: 12px 18px;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  .oriange {
    color: #7d5319;
  }

  .gray {
    color: #999999;
  }

  .blue {
    color: #3571d1;
  }

  .fruit_img {
    width: 79px;
    height: 79px;
    background-color: #edeeef;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 80%;
    }
  }

  .ltd {
    flex: 1;
    margin-left: 6px;
    font-size: 12px;

    .item {
      display: flex;
      justify-content: flex-start;
      margin-bottom: 4px;

      span {
        line-height: 17px;
      }
    }

    .item:last-child {
      margin-bottom: 0;
    }
  }
}

.wl_detail {
  width: 100%;
  padding: 17px 20px;
  margin-bottom: 30px;
  margin-top: 9px;
  background-color: #ffffff;

  .title {
    line-height: 18px;
    font-size: 13px;
    color: #333333;
  }
}
.no_wl {
  padding: 0 18px;
  line-height: 20px;
  color: #333333;
  font-size: 14px;
  margin-top: 20px;
}
.step_box {
  width: 100%;
  margin-top: 16px;
  .step {
    width: 100%;
    position: relative;
    padding-left: 16px;
    padding-bottom: 16px;
    h3 {
      font-size: 13px;
      color: #333333;
      line-height: 18px;
    }
    p {
      font-size: 12px;
      color: #999999;
      line-height: 18px;
      margin-top: 4px;
    }
    .line {
      height: 80%;
      border-left: 1px solid #e2e2e2;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  .step.active {
    h3,
    p {
      color: #2962d1;
    }
  }
  .step:last-child .line {
    border: none;
  }
  .step::before {
    content: '';
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #d8d8d8;
    position: absolute;
    left: -4px;
    top: 5px;
    z-index: 2;
  }
  .step.success::before {
    content: '';
    width: 15px;
    height: 15px;
    position: absolute;
    left: -7.5px;
    background: url(./images/wl_1.png) no-repeat center center;
    background-size: 100% 100%;
    top: 0px;
  }
  .step.ck::before {
    content: '';
    width: 25px;
    height: 25px;
    position: absolute;
    left: -12.5px;
    background: url(./images/wl_2.png) no-repeat center center;
    background-size: 100% 100%;
    top: 0px;
  }
  .step.zf::before {
    content: '';
    width: 25px;
    height: 25px;
    position: absolute;
    left: -12.5px;
    background: url(./images/wl_4.png) no-repeat center center;
    background-size: 100% 100%;
    top: 0px;
  }
  .step.db::before {
    content: '';
    width: 25px;
    height: 25px;
    position: absolute;
    left: -12.5px;
    background: url(./images/wl_2.png) no-repeat center center;
    background-size: 100% 100%;
    top: 0px;
  }
}
</style>
