<script>
export default {
  props: {
    title: {
      type: String,
      default: '',
    },
    showTime: {
      type: Boolean,
      default: true,
    },
    isabout: {
      type: Boolean,
      default: false,
    },
    list: {
      type: Array,
      default: () => [],
    },
    showMore: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  methods: {
    moreHandle() {
      this.$emit('clickMore');
    },
    timeHandle(str) {
      let d = new Date(str);

      let batchDate =
        d.getFullYear() +
        '-' +
        (d.getMonth() + 1 < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1) +
        '-' +
        (d.getDate() < 10 ? '0' + d.getDate() : d.getDate());
      return batchDate;
    },
  },
};
</script>

