<template>
  <div class="content">
    <div class="index-content">
      <div class="index-headimg-box">
        {{ $t('lang.sign_pre') }}
        <div>{{ data.num }} {{ $t('lang.person') }}</div>
      </div>
    </div>
    <div class="index-challenge-btn">
      <div>
        <div class="index-challenge-a1">{{ $t('lang.sign_day') }}</div>
        <button
          class="index-challenge-btn-a1 open-popup"
          @click="checkin"
          v-bind:style="data.is_checkin ? 'background:#999' : ''"
        >
          <span> {{ $t('lang.sign_rn') }}</span>
        </button>
      </div>
    </div>
    <div class="index-item">
      <div class="rank">
        <div class="rank-item">
          {{ $t('lang.order') }}
        </div>
        <div class="rank-item">
          {{ $t('lang.user') }}
        </div>
        <div class="rank-item">
          {{ $t('lang.sign_times') }}
        </div>
        <div class="clearb"></div>
      </div>
      <div class="rank" v-for="(item, key) in data.list" :key="key">
        <div class="rank-item">
          <span class="span" :class="key < 3 ? 'span' + (key + 1) : 'item'">{{
            key + 1
          }}</span>
        </div>
        <div class="rank-item">{{ item.mobile }}</div>
        <div class="rank-item">{{ item.num }}</div>
        <div class="clearb"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {
        is_checkin: false,
      },
    };
  },
  created() {
    this.$parent.footer(true);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/info/checkinlist').then(res => {
        this.data = res.data;
      });
    },
    checkin() {
      if (this.data.is_checkin) {
        return;
      }
      Fetch('/user/info/checkin').then(res => {
        this.$parent.setFooter(res.data.footer_type);
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: '',
        });
        this.data.is_checkin = true;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.content {
  .index-content {
    color: #ffffff;
    width: 100%;
    background: #1fa3f8;

    .index-headimg-box {
      text-align: center;
      width: 80%;
      height: 200px;
      margin: 0 auto;
      padding-top: 50px;

      div {
        margin-top: 10px;
        color: #ffdd00;
      }
    }
  }

  .index-challenge-btn {
    text-align: center;
    height: 120px;

    .index-challenge-a1 {
      padding: 20px 0;
      font-size: 16px;
    }

    .index-challenge-btn-a1 {
      border-radius: 3px;
      background: #f6ab40;
      height: 50px;
      color: #fff;
      line-height: 30px;
      text-align: center;
      border: 0;
      margin-bottom: 10px;
      font-size: 15px;
      width: 90%;
    }
  }

  .index-item {
    padding: 10px 20px;
    text-align: center;
    /*height: 500px;*/

    .rank {
      height: 30px;

      div {
        height: 30px;
        padding: 5px 0;
        border-bottom: 1px solid #999;
        text-align: center;
        width: 33.33%;
        float: left;
        font-size: 14px;
        color: #424242;
        line-height: 18px;
      }

      .clearb {
        display: none;
      }

      .rank-item {
        .span {
          background: #999;
          border-radius: 3px;
          color: #fff;
          padding: 0 5px;
        }

        .span1 {
          background: #f54545;
        }

        .span2 {
          background: #ff8547;
        }

        .span3 {
          background: #ffac38;
        }
      }
    }
  }
}
</style>
