<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Tab, Tabs, Uploader, ActionSheet, Toast, Empty, List } from 'vant';

Vue.use(ActionSheet);

Vue.use(Uploader);

Vue.use(Tab);

Vue.use(Empty);

Vue.use(Toast);
import axios from 'axios';
Vue.use(Tabs).use(List);
export default {
  components: {},
  data() {
    return {
      txtVal: 0,
      form: {
        type: 'A',
        content: '',
        image: [],
        name: '',
        mobile: '',
      },
      type: '',
      typeFont: '',
      active: 't1',
      fileList: [],
      customList: [],
      type_show: false,
      loading: false,
      finished: false,
      page_num: 10,
      page_curren: 1,
      count: 0,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {},
  methods: {
    getSearch() {
      Fetch('/user/replyback/search', {
        type: this.type,
        page_curren: this.page_curren,
      }).then(r => {
        r.data.list.forEach(item => {
          this.customList.push(item);
          this.count = r.data.count;
        });
        console.log(this.customList);
      });
    },
    onLoad() {
      this.page_curren++;
      this.getSearch();

      // 
      this.loading = false;

      // 
      if (this.customList.length >= this.count) {
        this.finished = true;
      }
    },
    changeType(t) {
      this.form.type = t;
    },
    changeCusType(T, f) {
      this.type = T;
      this.typeFont = f;
      this.customList = [];
      this.page_curren = 1;
      this.getSearch();
      this.type_show = false;
    },
    beforeRead(file) {
      if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
        this.$notify({
          message: this.$t('lang.type_limit'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return false;
      }
      return true;
    },
    afterRead(file) {
      console.log(file);
      let formData = new FormData();
      formData.append('file', file.file);
      formData.append('ssid', localStorage.getItem('ssid'));
      axios.post('/api/api/upload', formData).then(r => {
        if (r.data.code === 200) {
          this.form.image = r.data.data.file;
        } else {
          this.$notify({
            message: this.$t('lang.upload_fail'),
            color: '#FF3232',
            background: '#FFEAEA',
          });
        }
      });
    },
    changeTab() {
      if (this.active === 't2') {
        this.getSearch();
      }
    },

    descInput() {
      console.log(this.form.content);
      this.txtVal = this.form.content.length;
      // if(this.form.content.length > 200){

      // }
    },

    submit() {
      if (!this.form.content || !this.form.name || !this.form.mobile) {
        this.$notify({
          message: this.$t('lang.input_all_info'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      Fetch('/user/replyback/add', this.form)
        .then(r => {
          this.active = 't2';
          this.form = {
            type: 'A',
            content: '',
            image: [],
            name: '',
            mobile: '',
          };
          this.fileList = [];
          this.getSearch();
        })
        .catch(() => {
          this.$notify({
            message: this.$t('lang.submit_fail'),
            color: '#FF3232',
            background: '#FFEAEA',
          });
        });
    },
  },
};
</script>

