<script>
import Vue from 'vue';
import ProgressBar from './progressbar';
import { Dialog } from 'vant';
export default {
  props: {
    list: {
      type: Array,
      default: () => [],
    },
  },
  components: {
    ProgressBar,
  },
  data() {
    return {};
  },
  methods: {
    schedule(schedule) {
      return {
        width: schedule + '%',
      };
    },
    nameHandle(str) {
      let s = str.split('');
      return `<h5>${s[0]}</h5><p>${s[1]}</p>`;
    },
    goToDetail(payload) {
      if (~~payload.schedule >= 100) {
        Dialog.alert({
          title: this.$t('lang.note'),
          message: this.$t('lang.sela_all'),
          confirmButtonText: this.$t('lang.agree_s'),
        });
        return;
      }
      this.$router.replace('/item/' + payload.id);
    },
    schedule_left(schedule) {
      if (schedule > 80) {
        return {
          left: schedule - 30 + '%',
          color: '#fff',
        };
      } else {
        return {
          left: schedule.slice(0, 5) + '%',
        };
      }
    },
  },
};
</script>

