<template>
  <div class="codepay" :class="channel === 'wx' ? 'wx_bg' : 'zfb_bg'">
    <div class="header">
      <div class="head">
        <router-link to="/recharge" class="back"></router-link>
        {{ channel === 'wx' ? '' : '' }}{{ $t('lang.scran_charge') }}
      </div>
    </div>
    <div class="mobile" v-if="channel === 'wx'">
      <div class="header-nbsp">
        <div class="recharge_num">
          {{ $t('lang.wechat_charge') }} {{ money }} {{ $t('lang.rmb') }}
        </div>
        <div class="code_img">
          <img :src="qrcodeImg" width="100%" />
        </div>
        <div class="sub_box">
          <input
            type="text"
            placeholder=""
            v-model.trim="sub_num"
          />
        </div>
        <div class="sub" @click="sub_btn">{{ $t('lang.submit') }}</div>
      </div>
      <div class="content">
        {{ content }}
      </div>
    </div>
    <div class="mobile" v-if="channel === 'alipay'">
      <div class="header-nbsp">
        <div class="recharge_num">
          {{ $t('lang.alipay_charge') }} {{ money }} {{ $t('lang.rmb') }}
        </div>
        <div class="code_img">
          <img :src="qrcodeImg" width="100%" />
        </div>
        <div class="sub_box">
          <input
            type="text"
            placeholder=""
            v-model.trim="sub_num"
          />
        </div>
        <div class="sub" @click="sub_btn">{{ $t('lang.submit') }}</div>
      </div>
      <div class="content">
        {{ content }}
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'alipay',
  data() {
    return {
      title: '',
      money: '',
      channel: '',
      qrcodeImg: '',
      content: '',
      sub_num: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.money = this.$route.query.money ? this.$route.query.money : '';
    this.channel = this.$route.query.channel ? this.$route.query.channel : '';
    this.getData();
  },
  methods: {
    getData() {
      Fetch('/user/invest/getInvateInfo', {
        money: this.money,
        channel: this.channel,
      }).then(res => {
        if (res.data) {
          this.title = res.data.title;
          this.qrcodeImg = res.data.image;
          this.content = res.data.content;
        }
      });
    },
    sub_btn() {
      if (this.sub_num == '') {
        this.$notify({
          message: this.$t('lang.input_account'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      Fetch('/user/invest/apply', {
        money: this.money,
        channel: this.channel,
        pay_account: this.sub_num,
      }).then(res => {
        this.sub_num = '';
        this.$notify({
          message: this.$t('lang.submit_success'),
          background: '#EAF1FF',
          color: '#3292FF',
        });
        this.$router.replace('/user');
      });
    },
  },
};
</script>

<style lang="less" scoped>
.codepay {
  min-height: 100%;

  * {
    font-family: ' Microsoft YaHei UI', '', 'PingFang SC', 'Heiti SC',
      ' Droid Sans';
  }

  .content {
    text-indent: 2em;
    padding: 20px;
    width: 90.66%;
    margin: 10px auto 0 auto;
    font-size: 14px;
    color: #999999;
    line-height: 26px;
    background-color: #ffffff;
    border-radius: 4px;
  }
}

.header {
  height: 50px;
  width: 100%;
  max-width: 750px;
}
.wx_bg .header .head,
.wx_bg {
  background: #3cb371;
}
.zfb_bg .header .head,
.zfb_bg {
  background: #158de9;
}
.header .head {
  position: relative;
  width: 100%;
  max-width: 750px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  font-size: 18px;
  color: #ffffff;
}
.sub_box {
  width: 256px;
  height: 41px;
  margin: 24px auto 20px;
  background: rgba(244, 244, 244, 1);
  border-radius: 4px;
  display: flex;
  align-items: center;
  input {
    width: 100%;
    padding: 0 7px;
    height: 17px;
    line-height: 17px;
    color: #333333;
    font-size: 12px;
  }
}
.mobile {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  font-size: 16px;
  overflow: hidden;
  padding-bottom: 30px;
}

.header-nbsp {
  width: 90.66%;
  background-color: #ffffff;
  margin: 20px auto 0 auto;
  border-radius: 4px;
  overflow: hidden;
}
.recharge_num {
  width: 100%;
  height: 21px;
  line-height: 21px;
  font-size: 15px;
  color: #333333;
  text-align: center;
  margin-top: 33px;
  font-weight: bold;
}
.code_img {
  width: 186px;
  height: 186px;
  margin: 30px auto 0 auto;
  img {
    width: 100%;
    height: 100%;
  }
}
.wx_bg .sub {
  background: #3cb371;
}
.zfb_bg .sub {
  background: #158de9;
}
.sub {
  width: 116px;
  height: 36px;
  border-radius: 5px;
  margin: 0 auto 25px auto;
  text-align: center;
  font-size: 17px;
  color: #ffffff;
  line-height: 36px;
}
</style>
