              src="./images/bianji.png"
              alt=""
            />
          </div>
          <div class="center_type">
            <div class="default_icon" @click="save(v)">
              <i
                class="def_icon"
                :class="v.is_default === 'Y' ? 'default' : ''"
              ></i>
              {{ $t('lang.default_add') }}
            </div>
            <div class="delBtn" @click="del(v.id)">
              <img src="./images/delet.png" alt="" />
              <p>{{ $t('lang.delete') }}</p>
            </div>
          </div>
        </div>
        <!-- <div class="item">
          <div class="newitem">
            <img src="./images/address1.png" alt="" />
            <div class="center" @click="$router.push('/editaddress/' + v.id)">
              <div class="center_top">
