                <span class="item_mobile">{{ v.tel }}</span>
              </div>
              <div class="center_bottom">
                <div class="default_tips scale" v-show="v.is_default === 'Y'">
                  <span>{{ $t('lang.default') }} </span>
                </div>
                <div class="types scale" v-show="v.tag">
                  <span>{{ v.tag }}</span>
                </div>
                {{ v.province }} {{ v.city }} {{ v.county }} {{ v.address }}
              </div>
            </div>
            <img src="./images/bianji.png" alt="" />
          </div>
          <div class="center_type">
            <div class="default_icon" @click="save(v)">
              <i
                class="def_icon"
                :class="v.is_default === 'Y' ? 'default' : ''"
