          </div>
        </div> -->
        <!-- <div class="del_right" slot="right" @click="del(v.id)">
          <i></i>
        </div> -->
      </van-swipe-cell>
    </div>
    <div class="button-wrap logout">
      <div class="button-btn" @click="logout">
        {{ $t('lang.logout') }}
      </div>
    </div>
    <div class="button-wrap">
      <div class="button-btn" @click="$router.push('/editaddress')">
        + {{ $t('lang.add_address') }}
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { SwipeCell } from 'vant';
import Fetch from '../../utils/fetch';

Vue.use(SwipeCell);

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      money_info: '',
      money: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/address/search').then(res => {
        this.data = res.data;
      });
    },
    del(id) {
      Fetch('/user/address/remove', {
        id: id,
      }).then(() => {
        Fetch('/user/address/search').then(res => {
          this.data = res.data;
        });
      });
    },
    save(val) {
      Fetch('/user/address/save', {
        addressDetail: val.address,
        areaCode: val.area_code,
        city: val.city,
        county: val.county,
        id: val.id,
        isDefault: true,
        name: val.name,
        postalCode: val.postal_code,
        province: val.province,
        tel: val.tel,
        tag: val.tag,
        tags: val.tags,
      }).then(() => {
        Fetch('/user/address/search').then(res => {
          this.data = res.data;
        });
      });
    },
    logout() {
      Fetch('/api/api/logout').then(() => {
        this.$parent.setFooterType('n2');
        localStorage.setItem('reset_time', 0);
        localStorage.setItem('ssid', '');
        this.$router.replace('/');
      });
    },
  },
};
</script>

