<template>
  <div class="recharge_wrap">
    <div class="header">
      <div class="head">
        <router-link to="/user" class="back"></router-link>
        {{ $t('lang.recharge') }}
      </div>
    </div>
    <form class="form" method="post" @submit.prevent="handleSubmit">
      <div class="money_box">
        <div class="title1">{{ $t('lang.recharge_amount') }}</div>
        <div class="amount_box">
          <span>{{ $t('lang.rmb') }}</span>
          <input
            type="number"
            step="0.01"
            name="money"
            v-model.number="money"
            :placeholder="
              $t('lang.min_recharge') + ': ' + data.invest_min_money
            "
          />
          <div
            class="clear_num"
            v-show="money > 0"
            @click="clear_num_handle"
          ></div>
        </div>
      </div>
      <div class="pay-type">
        <div>
          {{ $t('lang.access_money') }}
          <span>{{ $t('lang.rmb') }}{{ data.money }}</span>
        </div>
      </div>
      <div class="fgline"></div>
      <div class="box box2">
        <div class="title2">{{ $t('lang.choose_recharge_type') }}</div>
        <div class="recharge_box">
          <div
            class="recharge_item clearfix"
            v-for="(channel, key) in data.config"
            :key="key"
          >
            <input
              class="recharge_type"
              type="radio"
              :value="channel.key"
              v-model="type"
            />
            <!-- <span
              class="recharge_icon"
              :class="'recharge_' + channel.key"
            ></span> -->
            <div class="recharge_icon_box">
              <p class="recharge_info">{{ channel.name }}</p>
              <div
                class="bank-remark"
                v-if="
                  channel.key === 'bank' &&
                    channel.bank_is_back === 'Y' &&
                    channel.bank_back_apr > 0
                "
              >
                {{ $t('lang.stroke_limit') }}
                {{ channel.bank_back_apr }}% {{ $t('lang.rebate_offer') }}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="button-wrap">
        <button
          type="submit"
          :class="money > 0 ? '' : 'no_touch'"
          class="btn basic_btn_v2"
        >
          {{ $t('lang.recharge_now') }}
        </button>
      </div>
    </form>
    <div id="mui-player"></div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import MuiPlayer from 'mui-player';
import 'mui-player/dist/mui-player.min.css';
export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      type: '',
      money: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    var mp = new MuiPlayer({
      container: '#mui-player',
      title: '',
      src: '/media/kidkid888.R.mp4',
    });
    this.start();
  },
  methods: {
    start() {
      Fetch('/user/invest/channel').then(res => {
        this.data = res.data;
        this.data.invest_min_money = Number(res.data.invest_min_money);
      });
    },
    clear_num_handle() {
      this.money = '';
    },
    getClass(channel) {
      return 'recharge_online_' + channel;
    },
    handleSubmit() {
      if (!this.money) {
        this.$notify({
          message: this.$t('lang.input_money'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.type) {
        this.$notify({
          message: this.$t('lang.choose_recharge_type'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      if (this.money < this.data.invest_min_money) {
        this.$notify({
          message:
            this.$t('lang.min_recharge') +
            ' ' +
            this.$t('lang.rmb') +
            this.data.invest_min_money,
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (this.type == 'zalo') {
        Fetch('/api/api/pay777payment', {
          money: this.money,
          invest_min_money: this.data.invest_min_money,
          payname: 'zalo',
        })
          .then(res => {
            console.log(res);
            window.location.href = res.data.pageurl;
            // this.data = res.data;
            // this.data.invest_min_money = Number(res.data.invest_min_money);
          })
          .catch(function(error) {});
        //window.location.href = 'https://www.google.com';
        return;
      }

      if (this.type == 'momo') {
        Fetch('/api/api/pay777payment', {
          money: this.money,
          invest_min_money: this.data.invest_min_money,
          payname: 'momo',
        })
          .then(res => {
            console.log(res);
            window.location.href = res.data.pageurl;
          })
          .catch(function(error) {});
        //window.location.href = 'https://www.google.com';
        return;
      }

      if (this.type === 'bank') {
        this.$router.replace({
          path: '/recharge/bank',
          query: {
            money: this.money,
          },
        });
        return;
      }

      // this.$router.replace({
      //   path: 'pay',
      //   query: {
      //     money: this.money,
      //     channel: this.type,
      //   },
      // });
    },
  },
};
</script>

<style lang="less" scoped>
