  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border: none;
  outline: none;
  background-image: url('images/recharge_checkoff.png');
  background-size: 20px 20px;
  background-color: #fff;
  background-repeat: no-repeat;
  border-radius: 20px;
}

input[type='checkbox']:checked,
input[type='radio']:checked {
  background-image: url('images/recharge_checked.png');
}

.recharge_wrap {
  min-height: 100%;
  background: #ffffff;
}
.top {
  height: 160px;
  background: #544b5e;
}

.form .box {
  background: #fff;
}

.form .box1 {
  padding: 18px 17px 0 17px;
}
.fgline {
  width: 100%;
  height: 1px;
  background: rgba(247, 248, 249, 1);
}

.form .box2 {
  margin: auto;
  padding: 24px 18px 18px 18px;
  width: 93%;
}

.form .box .title1 {
  color: #333333;
  font-size: 16px;
}

.form .box .title2 {
  color: #333333;
  font-size: 16px;
  line-height: 22px;
  font-weight: bold;
}
.recharge_icon_box {
  float: left;
}
.form .box .recharge_box .recharge_item {
  padding: 18px 0;
  display: flex;
  align-items: center;
  .bank-remark {
    color: #999999;
    font-size: 13px;
    line-height: 18px;
    margin-top: 3px;
  }
}
