  padding: 0 24px;
  margin: auto;
  margin-top: 58px;
  width: 93%;
  box-sizing: border-box;
}
.pay-type {
  margin: auto;
  width: 93%;
}
.money_box .amount_box {
  width: 100%;
}
.money_box .amount_box span {
  background: none;
  font-size: 20px;
}
.form .box .recharge_box .recharge_item .recharge_icon {
  float: left;
  width: 25px;
  height: 25px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 13px;
}

.form .box .recharge_box .recharge_item .recharge_bank {
  width: 34px;
  height: 24px;
  // background: url(images/yinlian_icon.png) no-repeat top left;
  background-size: auto 100%;
}

.form .box .recharge_box .recharge_item .recharge_alipay {
  width: 34px;
  height: 32px;
  background: url(images/zhifubao_icon.png) no-repeat top left;
  background-size: auto 100%;
}

.form .box .recharge_box .recharge_item .recharge_wx {
  width: 34px;
  height: 30px;
  background: url(images/weixin_icon.png) no-repeat top left;
  background-size: auto 100%;
}

.form .box .recharge_box .recharge_item .recharge_online_wechat {
  width: 34px;
  height: 30px;
  background: url(images/weixin_icon.png) no-repeat top left;
  background-size: auto 100%;
}

.form .box .recharge_box .recharge_item .recharge_info {
  color: #333333;
  font-size: 15px;
  line-height: 21px;
  height: 21px;
}

.form .box .recharge_box .recharge_item .recharge_type {
  float: left;
  width: 23px;
  height: 23px;
  margin-top: 2px;
  margin-right: 13px;
  background-size: 100% 100%;
}

.form .btn {
  display: block;
  width: 345px;
  height: 48px;
  line-height: 48px;
  margin: 20px auto 0 auto;
}
.form .btn.no_num {
  opacity: 0.6;
  pointer-events: none;
}
.pay-type {
  padding: 0 24px;
  div {
    height: 48px;
    line-height: 48px;
    margin-top: 10px;
  }
  div:nth-child(1) {
    padding-left: 28px;
    background: url('./images/chong.png') no-repeat left center;
    background-size: 22px;
    font-size: 14px;
    color: rgba(102, 102, 102, 1);
  }
}
#app .button-wrap {
  position: static;
  background: none;
  box-shadow: none;
}
.form .btn {
  margin: auto;
  width: 85%;
  border-radius: 25px;
  background: linear-gradient(to right, #22bfbe, #19c099);
  color: #fff;
  opacity: 1;
}
.money_box .amount_box input {
  font-weight: bold;
}
</style>
