<template>
  <div class="content">
    <div class="header">
      <div class="head">
        <div @click="$router.back()" class="back"></div>
      </div>
    </div>
    <div class="resetpaypwd_wrap">
      <h5 class="set_tips">{{ $t('lang.set_pat_pw') }}</h5>
      <form class="form" @submit.prevent="handleSubmit">
        <label class="item">
          <span class="info">{{ $t('lang.login_pw') }} </span>
          <input
            :type="oldpwd_type"
            class="inp oldpwd"
            v-model.trim="passwd"
            :placeholder="$t('lang.input_login_pw')"
          />
          <span
            class="right_icon"
            :class="oldpwd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_passwd"
          ></span>
        </label>
        <label class="item">
          <span class="info">{{ $t('lang.pay_pw') }} </span>
          <input
            :type="newpwd_type"
            class="inp newpwd"
            v-model.trim="npasswd"
            :placeholder="$t('lang.input_pay_pw')"
          />
          <span
            class="right_icon"
            :class="newpwd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_npasswd"
          ></span>
        </label>
        <!-- <div class="content">
          <div class="triangle"></div>
          <div class="item_box">
            <div class="item_title"></div>
            <div class="item">
              <input
                type="password"
                class="inp newpwd"
                v-model.trim="pwd"
                placeholder=""
              />
              <span class="right"></span>
            </div>
          </div>
          <div class="item_box">
            <div class="item_title">   </div>
            <div class="item">
              <input
                type="password"
                class="inp confirm_newpwd"
                v-model.trim="npwd"
                placeholder=""
              />
              <span class="right"></span>
            </div>
          </div>
        </div> -->
      </form>
    </div>
    <button
      type="submit"
      @click="handleSubmit"
      class="sbtn basic_btn_v2"
      :class="passwd != '' && npasswd != '' ? '' : 'no_touch'"
    >
      {{ $t('lang.finish') }}
    </button>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      passwd: '',
      npasswd: '',
      oldpwd_type: 'password',
      newpwd_type: 'password',
      confirm_newpwd_type: 'password',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    // this.start();
  },
  methods: {
    show_passwd() {
      if (this.oldpwd_type == 'password') {
        this.oldpwd_type = 'text';
      } else {
        this.oldpwd_type = 'password';
      }
    },
    show_npasswd() {
      if (this.newpwd_type == 'password') {
        this.newpwd_type = 'text';
      } else {
        this.newpwd_type = 'password';
      }
    },
    show_snpasswd() {
      if (this.confirm_newpwd_type == 'password') {
        this.confirm_newpwd_type = 'text';
      } else {
        this.confirm_newpwd_type = 'password';
      }
    },
    handleSubmit() {
      if (!this.passwd) {
        this.$notify({
          message: this.$t('lang.input_pay_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.npasswd) {
        this.$notify({
          message: this.$t('lang.input_pay_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/user/info/repaypasswd', {
        passwd: this.passwd,
        npasswd: this.npasswd,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.edit_success'),
        });
        this.$router.back();
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/set.less';
.btn_content {
  margin-top: 180px;
}
</style>
