<script>
import Fetch from '../../utils/fetch';
import { Cell, CellGroup } from 'vant';
import Kefu from '@/components/kefu';

export default {
  name: 'user',
  components: {
    Cell,
    Kefu,
  },
  data() {
    return {
      data: {},
      show_kefu: true,
      msg_show: true,
      item_code: [],
      loading: false,
      msg_num: 0,
      isShop: false,
      config: {
        user_contract_link: '/',
        user_contract_ys_link: '/',
      },
      list: [
        { name: '', path: '/account', icon: 'anquanzhongxin' },
        { name: '', path: '', icon: 'jisuanqi' },
        { name: '', path: 'share', icon: 'yaoqing' },
      ],
      list2: [],
      show: false,
    };
  },
  created() {
    if (this.$parent.getFooterType() === 'n1') {
      this.$router.push('/info');
    } else {
      this.$parent.footer(true, 'user');
    }
    this.start();
  },
  mounted() {
    this.shopOpen();
    var that = this;
    window.addEventListener(
      'scroll',
      function() {
        if (!that.show_kefu) {
          return;
        }
        that.show_kefu = false;
      },
      false
    );
    document.addEventListener(
      'click',
      function(ev) {
        if (ev.target.className != 'kefu_img') {
          that.show_kefu = false;
        }
      },
      false
    );
  },
  methods: {
    kefu_to() {
      if (this.show_kefu) {
        this.$router.push('/kefu');
      }
      this.show_kefu = !this.show_kefu;
    },
    start() {
      Fetch('/user/info/index').then(res => {
        this.data = res.data;
      });
      Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.config = res.data;
        this.show = true;
      });
      var that = this;
      // Fetch('/article/about/search').then(res => {
      //   that.item_code = res.data.list;
      //   this.list2 = res.data.list.filter(item => {
      //     return item.is_show_index === 'Y';
      //   });
      //   that.loading = true;
      // });
      Fetch('/user/noticeread/search').then(res => {
        var msg_arr = 0;
        res.data.list.forEach(function(k, v) {
          if (!k.is_read) {
            msg_arr += 1;
          }
        });

        this.msg_num = msg_arr;
        if (msg_arr > 9) {
          this.msg_num = '9+';
        }
      });
    },

    logout() {
      Fetch('/api/api/logout').then(() => {
        this.$parent.setFooterType('n1');
        localStorage.setItem('reset_time', 0);
        localStorage.setItem('ssid', '');
        this.$router.replace('/');
      });
    },
    router_go(r) {
      if (!this.show) return;
      this.$router.push('/art/' + this.item_code[r].code);
    },
    about_font(n) {
      return this.item_code[n].title;
    },
    shopOpen() {
      this.$Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.isShop = res.data.store_switch;
      });
    },
  },
};
</script>

