<template>
  <div class="content">
    <div class="mt0">
      <div class="mt0_main">
        <div class="mt0_title">
          <a @click="$router.back()" class="back"></a>
          <h4>{{ $t('lang.buynow') }}</h4>
        </div>
        <div class="mt0_content">
          <div class="mt0_item">
            <div class="mt0_item_title">{{ $t('lang.access_amount') }}</div>
            <div class="mt0_item_money">{{ data.money }}</div>
          </div>
          <div class="lines"></div>
          <div class="mt0_item mt0_item_last">
            <div class="mt0_item_title">{{ $t('lang.canbuy_amount') }}</div>
            <div class="mt0_item_money">{{ data.kt_money }}</div>
            <!-- <div class="lines"></div> -->
          </div>
          <!-- <div class="little_tips"></div> -->
        </div>
      </div>
    </div>
    <div class="mt1">
      <!-- <div class="item_name">{{ data.name }}</div> -->
      <form method="post" class="form">
        <div class="top">
          <div class="money_box">
            <div class="title1">{{ $t('lang.buy_amount') }}</div>
            <div class="amount_box">
              <span></span>
              <input
                type="number"
                v-model="money"
                :placeholder="
                  $t('lang.min_amount') + ' ' + $t('lang.rmb') + data.min_money
                "
                @input="inputHandle"
                name="money"
                id="money"
                class="bid_money"
              />
            </div>
            <div class="line"></div>
          </div>
          <div class="form_item">
            <div class="circle" />
            <div class="form_item_left">{{ $t('lang.cal_time') }} :</div>
            <div>
              {{ $t('lang.full') }}
              <span class="yellow">{{ $t('lang.oneday') }} </span
              >{{ $t('lang.auto_cal') }}
            </div>
          </div>
          <div class="form_item" v-show="is_vouchers">
            <div class="form_item_left">{{ $t('lang.coupon') }}</div>
            <div>
              <input class="radio_inp" type="radio" value="0" v-model="shiji" />
              <label for="one">{{ $t('lang.not_use') }} </label>

              <input
                class="radio_inp"
                type="radio"
                :value="is_vouchers_money"
                v-model="shiji"
              />
              <label for="two">{{ is_vouchers_money }}</label>
            </div>
          </div>
          <div v-show="parseFloat(money) >= parseFloat(is_vouchers_money)">
            <div class="form_item1" v-show="is_vouchers">
              <div>
                {{ $t('lang.act_min') }} {{ money - shiji }}{{ $t('lang.rmb') }}
              </div>
            </div>
          </div>
        </div>

        <!-- <div class="pay_box">
          <span class="left_icon"></span>
          <input
            :type="psd_type"
            class="inp_pay"
            v-model.trim="passwd"
            placeholder=""
          />
          <span
            class="right_icon"
            :class="psd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_psd"
          ></span>
        </div> -->
        <!-- <div class="forgetpay">
          <div class="btn" @click="$router.push('/resetpaypwd')">?</div>
        </div> -->
      </form>
    </div>
    <div class="btn_wrap">
      <button
        type="submit"
        class="btn basic_btn_v2"
        @click="psd_pop = true"
        :class="money > 0 ? '' : 'no_touch'"
      >
        {{ $t('lang.confirm') }}
        <!--  -->
      </button>
    </div>
    <van-dialog
      v-model="psd_pop"
      :title="$t('lang.input_pw')"
      :showConfirmButton="false"
    >
      <div class="pay_box">
        <input
          :type="psd_type"
          class="inp_pay"
          v-model.trim="passwd"
          :placeholder="$t('lang.input_sale_pw')"
        />
        <span
          class="right_icon"
          :class="psd_type == 'password' ? '' : 'right_icon_open'"
          @click="show_psd"
        ></span>
      </div>
      <div class="forgetpay">
        <div class="btn" @click="$router.push('/resetpaypwd')">
          {{ $t('lang.fotget_pw') }}
        </div>
      </div>
      <div class="button_wrap">
        <div
          class="cancel"
          @click="
            psd_pop = false;
            passwd = '';
          "
        >
          {{ $t('lang.cancel') }}
          <!--  -->
        </div>
        <div class="confirm" @click="handleSubmit">
          {{ $t('lang.confirm') }}
        </div>
      </div>
    </van-dialog>
    <van-dialog v-model="success_pop" :showConfirmButton="false">
      <div class="success_icon"></div>
      <div class="success_tips">
        {{ $t('lang.con_success') }}
      </div>
      <div class="button_wrap">
        <div class="cancel" @click="successOk">
          {{ $t('lang.confirm') }}
        </div>
        <div class="confirm" @click="lookHT">{{ $t('lang.view_coo') }}</div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Dialog } from 'vant';
Vue.use(Dialog);
export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      money: '',
      passwd: '',
      id: 0,
      is_vouchers: false,
      is_vouchers_money: 0,
      shiji: '0',
      now_money_num: 0,
      psd_type: 'password',
      psd_pop: false,
      success_pop: false,
      cid: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.id = this.$router.history.current.params.id;
    this.start();
  },
  methods: {
    clear_num_handle() {
      this.money = '';
    },
    show_psd() {
      if (this.psd_type == 'password') {
        this.psd_type = 'text';
      } else {
        this.psd_type = 'password';
      }
    },
    inputHandle(ev) {
      var number = parseFloat(ev.target.value);
      if (number >= parseFloat(this.data.max_money)) {
        this.money = parseFloat(this.data.max_money);
      }
      // if(number<=parseFloat(this.data.min_money)){
      // 	this.money = parseFloat(this.data.min_money);
      // }
    },
    start() {
      Fetch('/item/dq/index', {
        id: this.id,
      }).then(res => {
        this.data = res.data;
        // this.money = res.data.min_money;
        this.is_vouchers_money = res.data.vouchers_money;
        if (res.data.vouchers_money > 0) {
          this.is_vouchers = true;
        } else {
          this.is_vouchers = false;
        }

        this.now_money_num = this.money;
      });
    },
    back() {
      this.$router.push('/item/' + this.id);
    },
    getClass(channel) {
      return 'recharge_online_' + channel;
    },
    handleSubmit() {
      var djq_num = 0;
      if (this.shiji == 0) {
        djq_num = 0;
      } else {
        djq_num = this.is_vouchers_money;
      }
      if (!this.money) {
        this.$notify({
          message: this.$t('lang.input_i_amount'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.passwd) {
        this.$notify({
          message: this.$t('lang.input_sale_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      Fetch('/item/dq/apply', {
        id: this.id,
        money: this.money,
        passwd: this.passwd,
        vouchers_money: djq_num,
      }).then(r => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.con_success'),
        });
        this.success_pop = true;
        this.cid = r.data.cid;

        // Dialog.alert({
        //   message: '',
        //   showConfirmButton: true,
        //   showCancelButton: true,
        //   confirmButtonText: '',
        //   cancelButtonText: '',
        // })
        //   .then(() => {
        //     this.$router.replace('/contract/' + r.data.cid);
        //   })
        //   .catch(() => {
        //     this.$router.replace('/touzi');
        //   });
      });
    },
    successOk() {
      this.success_pop = false;
      this.$router.replace('/touzi');
    },
    lookHT() {
      this.success_pop = false;
      this.$router.replace('/contract/' + this.cid);
    },
  },
};
</script>

