            class="tab"
            :class="status === k ? 'tab_active' : ''"
            v-for="(v, k) in data.config.status"
            :key="k"
            @click="start(k)"
          >
            <div class="info">{{ v }}</div>
            <div class="line"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="order_wrap">
      <div class="ctn">
        <div class="item" v-for="(v, k) in data.list" :key="k">
          <!--  -->
          <div class="left">
            <img :src="v.thumb" />
          </div>
          <div class="right">
            <div class="title">{{ v.goods_id_title }}</div>
            <!-- <div class="sub">{{v.remark}}</div> -->
            <div class="dh_time">
              {{ $t('lang.exchange_time') }} {{ timer(v.addtime) }}
            </div>
            <div class="price_box">
              <span class="rmb"></span>
              <span class="price">x1</span>
            </div>
            <!--  <div class="x1"></div>
            <div class="btn_box">
              <a href="javascript:;" v-if="v.status != 'D'" class="not_ship">
                {{ v.status_name }}
              </a>
            </div> -->
          </div>
