<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { NoticeBar } from 'vant';

Vue.use(NoticeBar);

export default {
  name: 'info',
  data() {
    return {
      data: {
        config: {
          status: {},
        },
      },
      status: 'S',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start(status = 'S') {
      this.status = status;
      Fetch('/user/order/search', { status: status }).then(res => {
        this.data = res.data;
      });
    },
    goToDetail(id, dh, img) {
      if (dh) {
        this.$router.push({
          path: `/order/${id}/${dh}`,
        });
        localStorage.setItem('imgs', img);
      } else {
        this.$notify({
          message: this.$t('lang.logistics_error'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
      }
    },
    timer(t) {
      var time = new Date(t * 1000);
      return (
        time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate()
      );
    },
  },
};
</script>

