<template>
  <div class="cash_wrap">
    <div class="header">
      <div class="head">
        <a @click="$router.back()" class="back"></a>
        {{ $t('lang.withdrawal') }}
      </div>
      <!-- <div class="tx_tips" @click="show_bank_tips"></div> -->
    </div>
    <div class="withdraw_wrap">
      <form method="post" class="form">
        <div class="bank_box" @click="is_bank" v-if="bank.id">
          <p class="dz_tip">{{ $t('lang.income_bankcard') }}</p>
          <div class="bank_icon_wrap">
            <img :src="bg" class="bank_icon" alt="" />
          </div>
          <div class="left">
            <div class="left_bank_name">{{ bank.name }}</div>
            <div class="left_bank_number">{{ bank.card }}</div>
          </div>
          <div class="right">
            <span class="right_arrow"></span>
          </div>
        </div>
        <div class="bank_box" v-else @click="is_bank">
          <div class="add_wrap">
            <span class="add_icon">+</span>
            <span class="add_info">{{ $t('lang.add_bankcard') }} </span>
          </div>
        </div>
        <!-- <div class="withdraw_warming"> -->
        <!-- <span class="left"></span> -->
        <!-- <span class="right">200</span> -->
        <!-- </div> -->
        <div class="money_box">
          <div class="title1">{{ $t('lang.withdrawal_money') }}</div>
          <div class="amount_box">
            <span></span>
            <input
              class="inp inp_money"
              type="number"
              v-model="money"
              name="money"
              :placeholder="$t('lang.input_money')"
            />
            <div
              class="clear_num"
              v-show="money > 0"
              @click="clear_num_handle"
            ></div>
          </div>
          <div class="line"></div>
        </div>
        <div class="pay-type">
          <div>
            {{ $t('lang.access_money') }}
            <span>{{ data.money }}</span>
            <!-- <i @click="money = data.money"></i> -->
          </div>
        </div>
        <div class="tips_content">
          <h3>{{ $t('lang.note') }}</h3>
          <p>
            {{ $t('lang.note1') }}
          </p>
          <p>
            {{ $t('lang.note2') }}
          </p>
          <p>{{ $t('lang.note3') }}</p>
        </div>
        <!-- <div class="pay_box">
          <span class="left_icon"></span>
          <input
            :type="psd_type"
            class="inp_pay"
            v-model.trim="passwd"
            placeholder=""
          />
          <span
            class="right_icon"
            :class="psd_type == 'password' ? '' : 'right_icon_open'"
            @click="show_psd"
          ></span>
        </div>
        <div class="forgetpay">
          <div class="btn" @click="$router.push('/resetpaypwd')">?</div>
        </div> -->
      </form>
    </div>
    <div class="button-wrap">
      <button
        @click="psd_pop = true"
        class="sbtn basic_btn_v2"
        :class="money > 0 ? '' : 'no_touch'"
      >
        {{ $t('lang.withdrawal_r') }}
      </button>
    </div>
    <van-popup
      v-model="show"
      position="bottom"
      round
      closeable
      close-icon-position="top-left"
      style="height: 60%"
    >
      <div class="bank_title">
        <p>{{ $t('lang.choose_bankcard') }}</p>
      </div>
      <bank-select :onChange="selected" />
    </van-popup>
    <van-dialog
      v-model="psd_pop"
      :title="$t('lang.input_pw')"
      :showConfirmButton="false"
    >
      <div class="pay_box">
        <input
          :type="psd_type"
          class="inp_pay"
          v-model.trim="passwd"
          :placeholder="$t('lang.input_sale_pw')"
        />
        <span
          class="right_icon"
          :class="psd_type == 'password' ? '' : 'right_icon_open'"
          @click="show_psd"
        ></span>
      </div>
      <div class="forgetpay">
        <div class="btn" @click="$router.push('/resetpaypwd')">
          {{ $t('lang.fotget_pw') }} ?
        </div>
      </div>
      <div class="button_wrap">
        <div
          class="cancel"
          @click="
            psd_pop = false;
            passwd = '';
          "
        >
          {{ $t('lang.cancel') }}
        </div>
        <div class="confirm" @click="handleSubmit">
          {{ $t('lang.confirm') }}
        </div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import Vue from 'vue';
import Fetch from '../../utils/fetch';
import { Popup, Dialog } from 'vant';
import select from './select';
Vue.use(Dialog);
Vue.use(Popup);

export default {
  name: 'index',
  components: {
    'bank-select': select,
  },
  data() {
    return {
      show: false,
      data: {},
      bank: {},
      bank_id: 0,
      money: '',
      passwd: '',
      bg: '',
      psd_type: 'password',
      bank_tips: false,
      psd_pop: false,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      var that = this;
      Fetch('/user/cost/index')
        .then(res => {
          this.data = res.data;
          this.bank = res.data.bank;
        })
        .then(function() {
          if (that.bank.id) {
            that.choose_bank(that.bank.name);
          }
        });
    },
    is_bank() {
      if (this.bank.id) {
        this.show = true;
      } else {
        this.$router.push('/add_bank');
      }
    },
    clear_num_handle() {
      this.money = '';
    },
    show_psd() {
      if (this.psd_type == 'password') {
        this.psd_type = 'text';
      } else {
        this.psd_type = 'password';
      }
    },
    show_bank_tips() {
      this.bank_tips = true;
    },
    close_bank_tips() {
      this.bank_tips = false;
    },
    choose_bank(type) {
      this.bg = require('./images/' + this.bank_bg(type) + '_icon.png');
    },
    add_bank() {
      this.$router.push('/bank');
    },
    bank_bg(type) {
      if (type.indexOf('') != -1) {
        return 'gongshang';
      } else if (type.indexOf('') != -1) {
        return 'beijing';
      } else if (type.indexOf('') != -1) {
        return 'guangda';
      } else if (type.indexOf('') != -1) {
        return 'guangfa';
      } else if (type.indexOf('') != -1) {
        return 'huaxia';
      } else if (type.indexOf('') != -1) {
        return 'jianshe';
      } else if (type.indexOf('') != -1) {
        return 'jiaotong';
      } else if (type.indexOf('') != -1) {
        return 'jinkou';
      } else if (type.indexOf('') != -1) {
        return 'minsheng';
      } else if (type.indexOf('') != -1) {
        return 'nongye';
      } else if (type.indexOf('') != -1) {
        return 'pufa';
      } else if (type.indexOf('') != -1) {
        return 'xingye';
      } else if (type.indexOf('') != -1) {
        return 'youzheng';
      } else if (type.indexOf('') != -1) {
        return 'zhaoshang';
      } else if (type.indexOf('') != -1) {
        return 'zhongguo';
      } else if (type.indexOf('') != -1) {
        return 'zhongxin';
      } else {
        return 'basic_bank';
      }
    },
    handleSubmit() {
      if (!this.bank.id) {
        this.$notify({
          message: this.$t('lang.choose_withdrawal_bank'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.money) {
        this.$notify({
          message: this.$t('lang.input_withdrawal_money'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/user/cost/apply', {
        money: this.money,
        bank_id: this.bank.id,
        passwd: this.passwd,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.withdrawal_success'),
        });

        this.$router.back();
      });
    },
    selected(bank) {
      this.show = false;
      this.bank = bank;
      this.choose_bank(this.bank.name);
    },
  },
};
</script>

