<template>
  <van-list :finished="true">
    <div class="bank_box" v-for="(v, k) in list" :key="k" @click="onChange(v)">
      <div class="bank_icon_left">
        <img :src="choose_bank(v.name)" class="bank_icon" alt="" />
      </div>
      <div class="item">
        <div class="bank_info">
          <div class="bank_name">{{ v.name }} {{ $t('lang.bank_chuxu') }}(3667)</div>
          <!-- <div class="bank_number">{{ v.card }}</div> -->
        </div>
      </div>
    </div>
    <!-- <router-link class="btn" to="/bank"></router-link> -->
    <div class="bank_add" @click="$router.push('/bank')">
      <img src="./images/chong.png" alt="" />
      <div class="bank_tip">
        {{ $t('lang.use_new_card') }} <span class="right"></span>
      </div>
    </div>
  </van-list>
</template>
<script>
import Vue from 'vue';
import { List } from 'vant';
import Fetch from '../../utils/fetch';

Vue.use(List);
export default {
  props: {
    onChange: {
      type: Function,
      required: true,
    },
  },
  data() {
    return {
      list: [],
      bg: '',
    };
  },
  mounted() {
    Fetch('/user/bank/search').then(r => {
      this.list = r.data.list;
    });
  },
  methods: {
    choose_bank(type) {
      return require('./images/' + this.bank_bg(type) + '_icon.png');
    },
    bank_bg(type) {
      if (type.indexOf('') != -1) {
        return 'gongshang';
      } else if (type.indexOf('') != -1) {
        return 'beijing';
      } else if (type.indexOf('') != -1) {
        return 'guangda';
      } else if (type.indexOf('') != -1) {
        return 'guangfa';
      } else if (type.indexOf('') != -1) {
        return 'huaxia';
      } else if (type.indexOf('') != -1) {
        return 'jianshe';
      } else if (type.indexOf('') != -1) {
        return 'jiaotong';
      } else if (type.indexOf('') != -1) {
        return 'jinkou';
      } else if (type.indexOf('') != -1) {
        return 'minsheng';
      } else if (type.indexOf('') != -1) {
        return 'nongye';
      } else if (type.indexOf('') != -1) {
        return 'pufa';
      } else if (type.indexOf('') != -1) {
        return 'xingye';
      } else if (type.indexOf('') != -1) {
        return 'youzheng';
      } else if (type.indexOf('') != -1) {
        return 'zhaoshang';
      } else if (type.indexOf('') != -1) {
        return 'zhongguo';
      } else if (type.indexOf('') != -1) {
        return 'zhongxin';
      } else {
        return 'basic_bank';
      }
    },
  },
};
</script>
