<template>
  <div
    v-if="msg_show"
    class="kefu"
    @click="kefu_to"
    :class="show_kefu ? '' : 'kefu_hide'"
  >
    <img class="kefu_img" src="../views/index/image/kefu2.png" />
  </div>
</template>

<script>
export default {
  name: 'kefu',
  props: {},
  data() {
    return {
      show_kefu: false,
      msg_show: true,
    };
  },
  watch: {
    $route(t, f) {
      if (t.path.indexOf('tree') != -1 || t.path.indexOf('kefu') != -1) {
        this.msg_show = false;
      } else {
        this.msg_show = true;
      }
    },
  },
  mounted() {
    var that = this;
    window.addEventListener(
      'scroll',
      function() {
        if (!that.show_kefu) {
          return;
        }
        that.show_kefu = false;
      },
      false
    );
    document.addEventListener(
      'click',
      function(ev) {
        if (ev.target.className != 'kefu_img') {
          that.show_kefu = false;
        }
      },
      false
    );
  },
  methods: {
    kefu_to() {
      if (this.show_kefu) {
        this.$router.push('/kefu');
      }
      this.show_kefu = !this.show_kefu;
    },
  },
};
</script>

<style lang="less" scoped>
.kefu {
  width: 80px;
  height: 80px;
  position: fixed;
  z-index: 10;
  right: 13px;
  bottom: 130px;
  transition: all 1s;
  opacity: 1;
  transform: translateX(0);
  z-index: 80;
  img {
    width: 70%;
    height: 70%;
  }
}
.kefu.kefu_hide {
  transform: translateX(70%);
  opacity: 0.5;
}
</style>
