<template>
  <div class="main">
    <div class="header">
      <van-nav-bar
        :title="$t('lang.cooperation')"
        left-arrow
        class="header-nav"
        fixed
        @click-left="$router.push('/user')"
      />
    </div>
    <div class="list">
      <div
        class="items"
        v-for="item in data"
        :key="item.id"
        @click="join(item)"
      >
        <img :src="item.thumb" alt="" />
        <p>{{ item.name }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Dialog } from 'vant';

Vue.use(Dialog);
export default {
  name: 'business',
  data() {
    return {
      data: [],
      show: true,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/article/businesscoo/search').then(r => {
        this.data = r.data.list;
      });
    },
    join(item) {
      if (item.status === 'Y') {
        this.$router.push('/business/join?id=' + item.id);
      } else {
        Dialog.alert({
          message: `${item.tel}\n\n${item.email}`,
          theme: 'round-button',
          confirmButtonText: '',
          confirmButtonColor: '#00B389',
          messageAlign: 'left',
        });
      }
    },
  },
};
</script>

