<template>
  <div class="main">
    <div class="header">
      <van-nav-bar
        :title="$t('lang.application')"
        left-arrow
        class="header-nav"
        fixed
        @click-left="$router.back()"
      />
    </div>
    <div class="form_box">
      <div class="item_list">
        <div class="items">
          <span class="left">{{ $t('lang.name') }}</span>
          <input
            type="text"
            v-model="form.name"
            :placeholder="$t('lang.real_name')"
          />
        </div>
        <div class="items">
          <span class="left">{{ $t('lang.company_name') }}</span>
          <input
            type="text"
            v-model="form.company"
            :placeholder="$t('lang.input_company_name')"
          />
        </div>
        <div class="items">
          <span class="left">{{ $t('lang.city') }}</span>
          <input
            type="text"
            v-model="form.city"
            :placeholder="$t('lang.input_city')"
          />
        </div>
        <div class="items bigitem">
          <p class="left">{{ $t('lang.is_industry') }}</p>
          <div class="radio_wrap">
            <van-radio-group v-model="form.status" direction="horizontal">
              <van-radio checked-color="#00B389" icon-size="16" name="Y">{{
                $t('lang.yes')
              }}</van-radio>
              <van-radio checked-color="#00B389" icon-size="16" name="N">{{
                $t('lang.no')
              }}</van-radio>
            </van-radio-group>
          </div>
        </div>
        <div class="items">
          <span class="left">{{ $t('lang.charge_mobile') }}</span>
          <input
            type="text"
            v-model="form.tel"
            :placeholder="$t('lang.input_mobile')"
          />
        </div>
      </div>
    </div>
    <div
      class="basic_btn_v2"
      @click="submit"
      :class="
        form.name && form.status && form.tel && form.city && form.company
          ? ''
          : 'no_touch'
      "
    >
      {{ $t('lang.confirm') }}
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { RadioGroup, Radio } from 'vant';

Vue.use(Radio);
Vue.use(RadioGroup);
export default {
  name: 'business',
  data() {
    return {
      data: [],
      form: {
        name: '',
        status: '',
        tel: '',
        city: '',
        company: '',
      },
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {},
  methods: {
    submit() {
      Fetch('/article/businesscoolist/apply', {
        ...this.form,
        pid: this.$route.query.id,
      }).then(r => {
        this.$notify({
          background: '#07c160',
          message: this.$t('lang.application_submit'),
        });
        this.$router.replace('/business');
      });
    },
  },
};
</script>

