<template>
  <div class="bank_big_wrap">
    <div class="header">
      <div class="head">
        <a @click="$router.back()" class="back"></a>
        {{ $t('lang.bank_card') }}
      </div>
    </div>
    <div class="bank_box">
      <div class="bank_wrap">
        <!-- <div class="bank_title">
          <span class="info1">{{ $t('lang.mybankcard') }} </span>
          <span class="info2"
            >{{ $t('lang.total') }} {{ data.count }}{{ $t('lang.unit_zhang') }}
          </span>
        </div> -->
        <div class="ctn">
          <van-swipe-cell
            right-width="46"
            @click="set_right(k)"
            @close="close_right(k)"
            v-for="(v, k) in data.list"
            :key="k"
          >
            <div class="item" :class="bank_bg(v.name)">
              <div class="bank_info">
                <div class="bank_name">
                 <i> {{ v.name }} </i>
                 <span>{{ $t('lang.bank_chuxu') }} </span>
                </div>
                <div class="bank_number">{{ v.card }}</div>
              </div>
            </div>
            <div class="delete_btn" slot="right" @click="delBank(v.id)">
              <i></i>
            </div>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <div class="addbank_box" @click="add_bank">
      <span class="add_icon">+</span>
      <span class="add_info">{{ $t('lang.add_bankcard') }} </span>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { SwipeCell } from 'vant';
import Fetch from '../../utils/fetch';

Vue.use(SwipeCell);

export default {
  name: 'index',
  data() {
    return {
      data: {},
      bank: {
        card: '',
        name: '',
      },
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    set_right(i) {
      $('.van-swipe-cell__right').css('right', '5px');
    },
    close_right(i) {
      $('.van-swipe-cell__right').css('right', '0px');
    },
    delBank(id) {
      Fetch('/user/bank/remove', { id: id }).then(() => {
        this.start();
      });
    },
    start() {
      Fetch('/user/bank/search').then(res => {
        this.data = res.data;
      });
    },
    add_bank() {
      this.$router.push('/add_bank');
    },
    bank_bg(type) {
      if (type.indexOf('') != -1) {
        return 'gongshang';
      } else if (type.indexOf('') != -1) {
        return 'beijing';
      } else if (type.indexOf('') != -1) {
        return 'guangda';
      } else if (type.indexOf('') != -1) {
        return 'huaxia';
      } else if (type.indexOf('') != -1) {
        return 'jianshe';
      } else if (type.indexOf('') != -1) {
        return 'jiaotong';
      } else if (type.indexOf('') != -1) {
        return 'jinkou';
      } else if (type.indexOf('') != -1) {
        return 'minsheng';
      } else if (type.indexOf('') != -1) {
        return 'nongye';
      } else if (type.indexOf('') != -1) {
        return 'pufa';
      } else if (type.indexOf('') != -1) {
        return 'xingye';
      } else if (type.indexOf('') != -1) {
        return 'youzheng';
      } else if (type.indexOf('') != -1) {
        return 'zhaoshang';
      } else if (type.indexOf('') != -1) {
        return 'zhongguo';
      } else if (type.indexOf('') != -1) {
        return 'zhongxin';
      } else {
        return 'basic_bank';
      }
    },
    handleSubmit() {
      if (!this.bank.name.replace(/\s/g, '')) {
        this.$notify({
          message: this.$t('lang.input_bankname'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      if (!this.bank.card.replace(/\s/g, '')) {
        this.$notify({
          message: this.$t('lang.input_bankcard'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
      Fetch('/user/bank/add', {
        ...this.bank,
      }).then(() => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.add_success'),
        });
        this.is_show = false;
        this.start();
      });
    },
  },
};
</script>

