<script>
import { Swipe, SwipeItem, NoticeBar, Overlay, Dialog } from 'vant';
import Swiper from 'swiper';
import point from './component/point';
import Kefu from '@/components/kefu';
import Fetch from '../../utils/fetch';
import focusList from '@/components/focusList';
import itemsList from '@/components/itemsList';
export default {
  name: 'index',
  components: {
    // VanSwipe: Swipe,
    // VanSwipeItem: SwipeItem,
    // focusList,
    Kefu,
    itemsList,
  },
  data() {
    return {
      images: [],
      current: 1,
      maxSwipe: 9,
      goods_list: [],
      style: {},
      opacity: 0,
      ad_show: false,
      show_kefu: true,
      ad: {},
      data: {
        app: {},
      },
      msg_show: true,
      down_show: true,
      fn: function() {},
      focus_list: [],
      hot_list: [],
      link_list: [],
      art_list: [],
      isShop: 'off',
      isActiveCn: localStorage.getItem('locale') == 'cn' ? true : false,
      isActiveYn: localStorage.getItem('locale') == 'yn' ? true : false,
      mySwiper: null,
    };
  },
  created() {
    // this.$nextTick(() => {
    //   this._initBody();
    // });

    this.$parent.footer(true, 'index');
    this.getBanner();
    this.start();
    this.getLink();
    this.getFocus();
    this.getArt();
    this.shopOpen();
  },
  mounted() {},
  methods: {
    // 
    switchLang(val) {
      let language = '';
      switch (val) {
        case 'cn':
          language = 'cn';
          break;
        case 'en':
          language = 'en';
          break;
        case 'yn':
          language = 'yn';
          break;
      }
      //   localstoragelocale
      localStorage.setItem('locale', language);
      // i8n
      this.$i18n.locale = val;
      //
      this.$router.go(0);
    },
    onMore() {
      this.$router.push({ name: 'point_mall' });
    },
    getArt() {
      this.$Fetch('/article/about/search', {
        type: 'index_article_config',
      }).then(res => {
        this.art_list = res.data.list.filter(item => {
          return item.is_show_index === 'Y';
        });
      });
    },
    onClick(item) {
      this.$router.push({ name: 'point_detail', query: { id: item.id } });
    },
    shopOpen() {
      this.$Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.isShop = res.data.store_switch;
        if (this.isShop === 'on') {
          this.getList();
        }
      });
    },
    getLink() {
      this.$Fetch('/api/api/webconfig', { type: 'links' }).then(res => {
        this.link_list = res.data.banner;
      });
    },
    getFocus() {
      this.$Fetch('/article/about/search', { type: 'focus' }).then(res => {
        var focus_list = res.data.list.filter(item => {
          return item.is_show_index === 'Y';
        });
        this.focus_list = focus_list;
      });
    },
    moreHandle() {
      this.$router.push('/focus');
    },
    kefu_to() {
      if (this.show_kefu) {
        this.$router.push('/kefu');
      }
      this.show_kefu = !this.show_kefu;
    },
    appdown_close() {
      this.data.app.is_disable = 'N';
    },
    start() {
      Fetch('/api/api/index').then(res => {
        this.ad = res.data.ad;
        this.data = res.data;
        this.hot_list = res.data.items;
        if (res.data.ad.is_disable == 'Y') {
          this.ad_show = true;
        } else {
          this.ad_show = false;
        }
      });
      var that = this;
      var bslua = navigator.userAgent;
      if (bslua.indexOf('bsl') != -1) {
        that.down_show = false;
      }
    },
    goAd() {
      this.close();
      window.location = this.ad.url;
      return false;
      //this.$router.push(this.ad.url);
    },
    close() {
      this.ad_show = false;
    },
    onChange(index) {
      let els = this.$refs.dian.querySelectorAll('div');
      for (let i = 0; i < els.length; i++) {
        els[i].className = '';
      }
      els[index].className = 'selected';
    },
    getBanner() {
      this.$Fetch('/api/api/webconfig', { type: 'banner' }).then(res => {
        this.images = res.data.banner;
        this.maxSwipe = res.data.banner.length;
        this.$nextTick(() => {
          this.initSwiper();
        });
      });
    },
    initSwiper() {
      this.mySwiper = new Swiper('.swiper-container', {
        direction: 'horizontal',
        loop: true,
        autoplay: true,
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 20,
        observer: true,
        observeParents: true,
        pagination: {
          el: '.swiper-pagination',
        },
      });
    },
    getList() {
      this.$Fetch('/goods/info/search', {
        order: 'id desc',
      }).then(res => {
        console.log(111);
        this.goods_list = res.data.list.slice(0, 4) || [];
      });
    },
    GoDetail(id) {
      console.log('id :>> ', id);
      this.$router.push({ name: 'point_detail', query: { id } });
    },
    _initBody() {
      const vm = this;
      this.fn = function scorllEvent() {
        vm.opacity = window.pageYOffset / 250;
        vm.style = { background: `rgba(0,0,0,${vm.opacity})` };
      };
      window.addEventListener('scroll', this.fn, false);
    },
    offScrollEvent() {
      window.removeEventListener('scroll', this.fn, false);
    },
  },
};
</script>
