<template>
  <div class="share_content">
    <van-nav-bar
      fixed
      :title="$t('lang.invite_friends')"
      left-arrow
      class="header-nav"
      :border="false"
      :right-text="$t('lang.s_rules')"
      @click-right="rule = true"
      @click-left="$router.push('/user')"
    />
    <!-- <div class="top" @click="showShare"> -->
    <div
      class="top"
      :style="'background: url(' + data.shareinfo + ') no-repeat center center'"
    >
      <div class="yao_wrap">
        <div
          class="yao_icon"
          :style="'background-image: url(' + data.share_image_url + ')'"
        ></div>
      </div>
    </div>
    <div>
      <img
        class="code-img"
        @touchstart="longpress"
        :src="data.share_image_url"
        alt=""
      />
    </div>

    <div>{{ data.mobile }}</div>
    <div>
      <div id="code">{{ data.mobile }}</div>
      <button
        id="copy_text"
        ref="copy"
        data-clipboard-action="copy"
        :data-clipboard-text="data.mobile"
        @click="copy"
      >
        {{ $t('lang.copy') }}
      </button>
    </div>
    <div class="main">
      <div class="my_invite">
        <div class="tit">{{ $t('lang.my_inveits') }}</div>
      </div>
      <div class="content">
        <div
          class="item clearfix"
          v-for="(item, key) in data.share_user"
          :key="key"
        >
          <div class="left 1">{{ item.mobile }}</div>
          <div class="right">{{ item.date }}</div>
        </div>
        <div class="no_data" v-show="data_length">
          <img src="./images/nodata.png" alt="" />
          <p>{{ $t('lang.nodata') }}</p>
        </div>
      </div>
    </div>
    <div class="qrcode_box" v-show="is_show_share">
      <div
        class="qrcode"
        :style="'background-image: url(' + data.share_image_url + ')'"
      ></div>
      <button type="button" class="qrcode_btn" @click="close">
        {{ $t('lang.got_it') }}
      </button>
    </div>
    <div class="mask_c" v-show="is_show_share"></div>

    <van-dialog
      v-model="rule"
      :title="$t('lang.rules')"
      confirmButtonColor="#00b389"
    >
      <div class="rule_msg" v-html="rulefont"></div>
    </van-dialog>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Dialog } from 'vant';

// 
Vue.use(Dialog);

export default {
  name: 'shares',
  components: {},
  data() {
    return {
      data: {},
      rule: false,
      is_show_share: false,
      data_length: false,
      rulefont: '',
      longClick: 0,
      timeOutEvent: 0,
    };
  },
  created() {
    this.$parent.footer(true, 'shares');
  },
  mounted() {
    this.start();
    this.$Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
      this.rulefont = res.data.invite_firends;
    });
  },
  methods: {
    // 
    longpress() {
      var that = this;
      this.longClick = 0;
      this.timeOutEvent = setTimeout(function() {
        that.longClick = 1;
        that.downImg();
      }, 500);
    },
    // 
    downImg() {
      if (!window.plus) return;
      window.plus.gallery.save(
        this.data.share_image_url,
        function() {
          // alert('!');
        },
        function() {
          // alert('!');
        }
      );
    },
    copy() {
      var clipboard = new this.Clipboard('#copy_text');
      clipboard.on('success', e => {
        clipboard.destroy();
      });
      clipboard.on('error', e => {
        // 
        // Message({
        //   message: '',
        //   type: 'warning',
        // });
        // 
        clipboard.destroy();
      });
    },
    close() {
      this.is_show_share = false;
    },
    showShare() {
      this.is_show_share = true;
    },
    start() {
      Fetch('/user/info/share').then(res => {
        console.log('res->', res);
        this.data = res.data;
        if (res.data.share_user.length == 0) {
          this.data_length = true;
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
html,
body {
  background: #e5e5e5;
}
.rule_msg {
  padding: 20px;
  white-space: pre-wrap;
  line-height: 26px;
}
.share_content {
  min-height: 100%;
  background: #fff;
  position: relative;
  padding-bottom: 30px;
}
/deep/ .van-dialog__header {
  color: rgb(0, 179, 137);
}

.top {
  width: 100%;
  height: 249px;
  // background: url(images/share_header.png) no-repeat center center;
  background-size: 100% 100% !important;
  margin: 0 auto;
  position: relative;
  .yao_wrap {
    width: 110px;
    height: 110px;
    background: #ffffff;
    box-shadow: 0px 2px 21px 0px rgba(0, 179, 137, 0.49);
    border-radius: 14px;
    position: absolute;
    left: 0;
    right: 0;
    top: 188px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
  }
  .yao_icon {
    background-size: 100% 100%;
    height: 90px;
    width: 90px;
  }
}

// .yao_icon {
//   animation: heartbeat 1s infinite;
//   -webkit-animation: heartbeat 1s infinite;
// }

@keyframes heartbeat {
  0% {
    transform: scale(0.8);
    opacity: 1;
  }

  25% {
    transform: scale(1, 1);
    opacity: 0.8;
  }

  100% {
    transform: scale(0.8);
    opacity: 1;
  }
}

.top .title {
  width: 55.4%;
  margin: 0 auto;
  height: 35px;
  line-height: 35px;
  text-align: center;
  color: #333333;
  font-size: 18px;
  background: #e8c27d;
  border-radius: 35px;
}

.top .avator_box {
  width: 51.8%;
  margin: 0 auto;
  position: relative;
  background: #e8c27d;
  border-radius: 15px;
  padding: 1px 0;
}

.top .avator_box .avatar {
  background-size: contain;
  //background-image: url(images/code.png);
  width: 96px;
  height: 96px;
  margin: 0 auto;
  border-radius: 50%;
  /*#background: #fff;*/
  background-repeat: no-repeat;
  background-position: center;
}

.top .avator_box .invite_btn {
  height: 22px;
  line-height: 22px;
  color: #fefefe;
  font-size: 12px;
  border-radius: 22px;
  width: 140px;
  text-align: center;
  background: #544b5e;
  margin: 7px auto 10px auto;
}

.top .avator_box .invite_title {
  height: 30px;
  line-height: 30px;
  color: #fefefe;
  font-size: 18px;
  border-radius: 30px;
  width: 115px;
  text-align: center;
  background: #544b5e;
  margin: -16px auto 10px auto;
  border: 1px solid #e8c27d;
}

.main {
  padding: 26px 30px;
  margin-top: 49px;
}

.main .my_invite {
  color: #333333;
  width: 226px;
  margin: 0 auto;
  height: 20px;
  line-height: 20px;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: space-around;

  .tit {
    font-weight: bold;
    margin: 8px;
  }

  .h_line {
    width: 48px;
    height: 2px;
  }

  .h_line.left {
    background: url(images/x_left.png) no-repeat center center;
    background-size: 100% 100%;
  }

  .h_line.right {
    background: url(images/x_right.png) no-repeat center center;
    background-size: 100% 100%;
  }
}

.main .content {
  padding-bottom: 10px;
  margin-top: 17px;
  min-height: 300px;
  padding-top: 1px;
}

.no_data {
  width: 130px;
  margin: 90px auto 0 auto;
  text-align: center;

  img {
    width: 100%;
  }

  p {
    font-size: 12px;
    color: #999999;
    margin-top: 10px;
  }
}

.main .content .item {
  border-bottom: 1px solid #edebeb;
  color: #999999;
  font-size: 14px;
  line-height: 20px;
  padding: 17px 0;
}

.main .content .item:last-child {
  border-bottom: none;
}

.main .content .item .title {
  font-size: 14px;
}

.main .content .item .left {
  float: left;
  width: 90px;
  color: #333333;
  text-align: center;
}

.main .content .item .right {
  float: right;
  width: 140px;
  text-align: center;
  font-size: 12px;
  color: #999999;
}

.mask_c {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: #000;
  opacity: 0.7;
  z-index: 11;
}

.qrcode_box {
  width: 375px;
  height: 426px;
  background-image: url(images/codemask.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -56%);
  transform: translate(-50%, -56%);
  z-index: 12;
}

.qrcode_box .qrcode {
  width: 137px;
  height: 137px;
  background-image: url(images/qrcode.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
  bottom: 44px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

@media screen and (min-width: 320px) and (max-width: 400px) {
  .qrcode_box {
    -webkit-transform: translate(-50%, -56%) scale(0.8);
    transform: translate(-50%, -56%) scale(0.8);
  }
}

.qrcode_box .qrcode_info {
  text-align: center;
  color: #fff;
  font-size: 13px;
  margin: 20px auto 12px auto;
}

.qrcode_box .qrcode_btn {
  position: absolute;
  background: linear-gradient(90deg, #0092fd 0%, #1e6cfd 100%);
  width: 187px;
  height: 47px;
  line-height: 47px;
  text-align: center;
  font-size: 17px;
  color: #ffffff;
  font-weight: bold;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: -60px;
}
</style>
