<template>
  <div class="main">
    <div class="header">
      <van-nav-bar
        :title="$t('lang.normal_question')"
        left-arrow
        class="header-nav"
        fixed
        @click-left="$router.back()"
      />
    </div>
    <div class="tabs">
      <div
        class="tabs_list"
        :class="idx === current ? 'cur' : ''"
        v-for="(item, idx) in answerType"
        :key="item.id"
        @click="checkType(item, idx)"
      >
        {{ item.title }}
      </div>
    </div>
    <div class="question_list" v-if="questionList.length != 0">
      <div class="items" v-for="(val, idx) in questionList" :key="idx">
        <div class="item_tit">
          <p>{{ val.problem }}</p>
          <van-icon :name="val.icon" @click="showContent(val)" />
        </div>
        <div class="answer" v-show="val.isshow">
          {{ val.content }}
        </div>
      </div>
    </div>
    <van-empty v-else :description="$t('lang.no_relation_question')" />
    <div class="footer">
      <div class="goto go1" @click="$router.push('/customer')">
        <i></i>
        {{ $t('lang.feedback_comments') }}
      </div>
      <div class="goto go2" @click="$router.push('/kefu')">
        <i></i>
        {{ $t('lang.online_customer') }}
      </div>
    </div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Icon } from 'vant';
import { Empty } from 'vant';

Vue.use(Empty);
Vue.use(Icon);
export default {
  name: 'question',
  data() {
    return {
      icon_name: 'arrow-down',
      current: 0,
      isshow: 0,
      answerType: [],
      questionList: [],
      pid: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.getproblem();
  },
  methods: {
    checkType(item, i) {
      this.current = i;
      this.pid = item.id;
      console.log(i);
      this.getanswerlist();
    },
    getproblem() {
      Fetch('/article/problem/search')
        .then(res => {
          this.answerType = res.data.list;
          this.answerType.unshift({
            id: '',
            title: this.$t('lang.all_question'),
          });
          console.log(this.answerType);
          if (this.answerType.length !== 0) {
            this.pid = this.answerType[0].id;
          }
        })
        .then(() => {
          console.log(this.answerType);
          this.getanswerlist();
        });
    },
    getanswerlist() {
      Fetch('/article/problemanswer/search', {
        pid: this.pid,
      }).then(res => {
        this.questionList = res.data.list;
        this.questionList.forEach(item => {
          item.isshow = false;
          item.icon = 'arrow-down';
        });
        this.questionList.push();
        console.log(this.questionList);
      });
    },
    showContent(v) {
      console.log(v);
      v.isshow = !v.isshow;
      if (v.isshow) {
        v.icon = 'arrow-up';
      } else {
        v.icon = 'arrow-down';
      }
      this.questionList.push();
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  min-height: 100vh;
  background: #f1f1f1;
  padding-bottom: 100px;
}
.tabs {
  height: 55px;
  background: #fff;
  padding: 0 12px;
  white-space: nowrap;
  overflow-x: scroll;
  display: flex;
  align-items: center;
  &::-webkit-scrollbar {
    display: none;
  }
  .tabs_list {
    padding: 0 12px;
    height: 25px;

    background: #f1f1f1;
    border-radius: 13px;
    color: #666;
    text-align: center;
    line-height: 25px;
    font-size: 14px;
    margin-right: 24px;
    &.cur {
      background: #00b389;
      color: #fff;
    }
  }
}
.question_list {
  margin-top: 10px;
  background: #fff;
  padding: 0 12px 10px 12px;
  .item_tit {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
  }
  .answer {
    border-top: 1px solid #f1f1f1;
    font-size: 12px;
    line-height: 16px;
    color: #666;
    white-space: pre-wrap;
    padding: 12px 0;
  }
}
.footer {
  height: 90px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 32px;
  .goto {
    width: 127px;
    height: 36px;
    border: 1px solid #9e9e9e;
    border-radius: 2px;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      width: 18px;
      height: 18px;
      margin-right: 5px;
    }
  }
  .go1 i {
    background: url('./images/bicon1.png') no-repeat center center;
    background-size: 100% 100%;
  }
  .go2 i {
    background: url('./images/bicon2.png') no-repeat center center;
    background-size: 100% 100%;
  }
}
</style>
