<template>
  <div class="content_bg">
    <div class="header">
      <div class="head">
        <a @click="$router.push('/info')" class="back"></a>
        {{ $t('lang.set') }}
      </div>
    </div>
    <div class="main">
      <div class="item item1 flex">
        <span>{{ $t('lang.avatar') }} </span>
        <div class="right_box">
          <van-uploader
            :before-read="beforeRead"
            :after-read="afterRead"
            :max-count="1"
          >
            <div class="h_img" icon="photo" type="primary">
              <img :src="header" alt="" />
            </div>
          </van-uploader>
          <!-- <span class="right_arrow"></span> -->
        </div>
      </div>
      <router-link to="/setnickname" class="item item2 flex">
        <span>{{ $t('lang.nickname') }} </span>
        <div class="right_box">
          <div class="nick_name">
            {{ nickname }}
          </div>
          <!-- <span class="right_arrow"></span> -->
        </div>
      </router-link>
      <router-link to="/address" class="item2 flex">
        <div class="shouhuo">
          <img src="./images/address.png" alt="" />
          <div class="shdetail">
            <div class="sh_top">
              <p></p>
              <span> </span>
              <i> </i>
            </div>
            <div class="sh_bot">
              <p></p>
            </div>
          </div>
          <span class="right_arrow"></span>
        </div>
      </router-link>
    </div>

    <div class="logout_btn" @click="logout">{{ $t('lang.logout') }}</div>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Uploader } from 'vant';
import axios from 'axios';
Vue.use(Uploader);

export default {
  name: 'setup',
  components: {},
  data() {
    return {
      nickname: '',
      header: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    beforeRead(file) {
      if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
        this.$notify({
          message: this.$t('lang.type_limit'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return false;
      }
      return true;
    },
    afterRead(file) {
      console.log(file);
      let formData = new FormData();
      formData.append('file', file.file);
      formData.append('ssid', localStorage.getItem('ssid'));
      axios.post('/api/api/upload', formData).then(r => {
        console.log(r);
        if (r.data.code === 200) {
          Fetch('/user/info/upavatar', {
            avatar: r.data.data.file,
          }).then(res => {
            this.$notify({
              background: '#EAF1FF',
              color: '#3292FF',
              message: this.$t('lang.edit_success'),
            });
            this.header = res.data.avatar;
          });
        } else {
          this.$notify({
            message: this.$t('lang.upload_fail'),
            color: '#FF3232',
            background: '#FFEAEA',
          });
        }
      });
    },
    start() {
      Fetch('/user/info/index').then(res => {
        this.header = res.data.avatar;
        this.nickname = res.data.nick_name;
      });
    },
    logout() {
      Fetch('/api/api/logout').then(() => {
        this.$parent.setFooterType('n2');
        localStorage.setItem('reset_time', 0);
        localStorage.setItem('ssid', '');
        this.$router.replace('/');
      });
    },
  },
};
</script>

