<template>
  <div class="bg">
    <div class="header">
      <div class="head">
        <router-link to="/index" class="back"></router-link>
        {{ $t('lang.earnings_calculator') }}
      </div>
    </div>
    <form class="form" @submit.prevent="submit">
      <div class="content">
        <div class="item_box">
          <div class="item_title">{{ $t('lang.station_amount') }}</div>
          <div class="item">
            <input
              type="number"
              class="inp oldpwd"
              name="amount"
              v-model.trim="money"
              :placeholder="$t('lang.intention_station_amount')"
            />
            <span class="right">{{ $t('lang.rmb') }}</span>
          </div>
        </div>
        <div class="item_box">
          <div class="item_title">{{ $t('lang.station_limiy') }}</div>
          <div class="item">
            <input
              type="number"
              class="inp newpwd"
              name="day"
              v-model.trim="days"
              :placeholder="$t('lang.intention_station_limiy')"
            />
            <span class="right danwei">{{ $t('lang.day') }}</span>
          </div>
        </div>
        <div class="item_box">
          <div class="item_title">{{ $t('lang.daily_rate') }}</div>
          <div class="item">
            <input
              type="text"
              class="inp confirm_newpwd"
              name="se"
              v-model.trim="apr"
              :placeholder="$t('lang.intention_daily_rate')"
            />
            <span class="right">%</span>
          </div>
        </div>
        <div class="item_box">
          <div class="item_title">{{ $t('lang.repayment') }}</div>
          <div class="item xiala">
            <select name="type" class="select_showbox" v-model="type">
              <option value="E">{{ $t('lang.repayment_1') }}</option>
              <option value="A">{{ $t('lang.repayment_2') }}</option>
              <option value="B">{{ $t('lang.repayment_3') }}</option>
              <option value="C">{{ $t('lang.repayment_4') }}</option>
              <option value="D">{{ $t('lang.repayment_5') }}</option>
            </select>
          </div>
        </div>
      </div>

      <div class="btn_box">
        <button type="button" @click="clear" class="btn btn2">
          {{ $t('lang.reset') }}
        </button>
        <button type="submit" class="btn btn1 basic_btn_v2">
          {{ $t('lang.start_cal') }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      apr: '',
      days: '',
      type: 'A',
      money: '',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  methods: {
    clear() {
      this.apr = '';
      this.days = '';
      this.type = 'A';
      this.money = '';
    },
    submit() {
      if (!this.money) {
        this.$notify({
          message: this.$t('lang.input_money'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.days) {
        this.$notify({
          message: this.$t('lang.input_days'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.apr) {
        this.$notify({
          message: this.$t('lang.input_apr'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      this.$router.replace({
        path: 'cal_result',
        query: {
          money: this.money,
          type: this.type,
          days: this.days,
          apr: this.apr,
        },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.bg {
  height: 100%;
  background: #f0f2f5;
  width: 100%;

  .title {
    text-align: center;
    color: #e8c27d;
    font-size: 28px;
    padding: 50px 0 32px 0;
  }

  .form {
    margin: 0 auto 0 auto;
  }

  .form .item_box {
    margin-top: 18px;
    background-color: #ffffff;
    border-radius: 4px;
    height: 45px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .form .item_box .item_title {
    width: 70px;
    line-height: 19px;
    color: #6b6b6b;
    font-size: 14px;
    margin-left: 14px;
  }

  .form .content {
    width: 100%;
    padding: 0 24px;
  }

  .form .item_box .item {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }

  .form .item_box .item .inp {
    font-size: 14px;
    height: 20px;
    line-height: 20px;
  }

  .select_showbox {
    font-size: 16px;
    color: #666666;
    flex: 1;
    padding: 0 10px;
    position: relative;
  }

  .form .item_box .item.xiala {
    position: relative;
  }

  .xiala::after {
    content: '';
    position: absolute;
    width: 20px;
    height: 9px;
    right: 10px;
    top: 50%;
    margin-top: -5px;
    opacity: 0.7;
    background: url(../user/images/xiala2.png) no-repeat center center;
    background-size: 100% 100%;
  }

  ::selection {
    background-color: #f2f2f2;
  }

  .form .item_box .item .right {
    display: inline-block;
    font-size: 14px;
    color: #333333;
    margin-right: 13px;
  }

  .form .btn_box {
    margin-top: 35px;
    display: flex;
    padding: 0 27px;
    justify-content: space-between;
  }

  .form .btn_box .btn {
    width: 41.2%;
    height: 47px;
    line-height: 47px;
    border-radius: 5px;
    text-align: center;
    font-size: 18px;
    margin: 0;
  }

  .btn2 {
    border: 1px solid rgba(224, 224, 224, 1);
  }
}
</style>
