<template>
  <div class="main">
    <div class="header">
      <div class="head">
        <router-link to="/user" class="back"></router-link>
        {{ $t('lang.change_pw') }}
      </div>
    </div>

    <form class="form register" @submit.prevent="handleSubmit">
      <!-- <div class="content clearfix">
        <span class="left_icon phone_icon"></span>
        <input
          class="inp"
          v-model.trim="data.mobile"
          type="text"
          placeholder=""
        />
      </div> -->
      <div class="content clearfix">
        <span class="left_icon email_icon"></span>
        <input
          class="inp phone"
          id="email"
          name="email"
          type="text"
          v-model.trim="data.email"
          :placeholder="$t('lang.input_email')"
        />
        <span class="right right_icon clear_icon" @click="clear()"></span>
      </div>
      <div class="content clearfix">
        <input
          class="inp inp1"
          type="text"
          v-model.trim="data.image_code"
          :placeholder="$t('lang.input_code')"
        />
        <img
          class="right click_code image_code"
          :src="base64Image"
          @click="getCode"
        />
      </div>
      <div class="content clearfix">
        <input
          class="inp inp1"
          v-model.trim="data.code"
          type="text"
          :placeholder="$t('lang.input_sms_code')"
        />
        <van-count-down :time="time" class="right getsms" @finish="timeCall">
          <template v-slot="timeData">
            <span class="item" @click="sendemailcode">{{
              timeData.seconds > 0 ? timeData.seconds : $t('lang.get_code')
            }}</span>
          </template>
        </van-count-down>
        <!-- <span class="right getsms" @click="sendcode">
          <van-count-down :time="time" @finish="timeCall">
            <template v-slot="timeData" class="sendCode">{{
              time > 0 ? timeData.seconds : ''
            }}</template>
          </van-count-down>
        </span> -->
      </div>
      <div class="content clearfix">
        <span class="left_icon pwd_icon"></span>
        <input
          class="inp"
          v-model.trim="data.passwd"
          :type="psd"
          :placeholder="$t('lang.new_pw')"
        />
        <span
          class="right_icon show_icon"
          :class="psd == 'password' ? '' : 'show'"
          @click="show_psd"
        ></span>
      </div>
      <div class="content clearfix">
        <span class="left_icon pwd_icon"></span>
        <input
          class="inp"
          v-model.trim="data.npasswd"
          :type="psd"
          :placeholder="$t('lang.confirm_pw')"
        />
        <span
          class="right_icon show_icon"
          :class="psd == 'password' ? '' : 'show'"
          @click="show_psd"
        ></span>
      </div>
      <div class="content btn_content clearfix">
        <button
          type="submit"
          class="btn basic_btn_v2"
          :class="data.mobile != '' && data.passwd != '' ? '' : 'no_touch'"
        >
          {{ $t('lang.chonfirm_edit') }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import Vue from 'vue';
import Fetch from '../../utils/fetch';
import { CountDown } from 'vant';

Vue.use(CountDown);

export default {
  name: 'index',
  data() {
    return {
      bg: '',
      data: {},
      base64Image: '',
      psd: 'password',
      time: 0,
      is_send: false,
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
    this.getCode();
  },
  methods: {
    show_psd() {
      console.log(this.psd);
      if (this.psd == 'password') {
        this.psd = 'text';
      } else {
        this.psd = 'password';
      }
    },
    start() {
      Fetch('/api/api/webconfig', { type: 'bg' }).then(res => {
        this.bg = 'url("' + res.data.forget + '")';
      });
    },
    getCode() {
      Fetch('/api/api/imageCode').then(res => {
        this.base64Image = res.data.code;
      });
    },
    timeCall() {
      this.is_send = false;
      this.time = 0;
    },
    sendemailcode() {
      if (this.is_send) {
        return;
      }

      if (!this.data.email) {
        this.$notify({
          message: this.$t('lang.input_email'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.image_code) {
        this.$notify({
          message: this.$t('lang.input_img_code'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      this.is_send = true;

      Fetch('/api/api/emailcode', {
        image_code: this.data.image_code,
        type: 'forgetpwd',
        mobile: this.data.mobile,
        email: this.data.email,
      })
        .then(() => {
          this.time = 60 * 1000;
          this.$notify({
            background: '#EAF1FF',
            color: '#3292FF',
            message: this.$t('lang.send_success'),
          });
          this.getCode();
        })
        .catch(() => {
          this.is_send = false;
          this.getCode();
        });
    },
    sendcode() {
      if (this.is_send) {
        return;
      }

      if (!this.data.mobile) {
        this.$notify({
          message: this.$t('lang.input_phone'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.image_code) {
        this.$notify({
          message: this.$t('lang.input_img_code'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      this.is_send = true;
      Fetch('/api/api/code', {
        image_code: this.data.image_code,
        type: 'forgetpwd',
        mobile: this.data.mobile,
      })
        .then(() => {
          this.time += 60 * 1000;
          this.$notify({
            background: '#EAF1FF',
            color: '#3292FF',
            message: this.$t('lang.send_success'),
          });
          this.getCode();
        })
        .catch(() => {
          this.is_send = false;
          this.getCode();
        });
    },
    handleSubmit() {
      if (!this.data.email) {
        this.$notify({
          message: this.$t('lang.input_email'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.passwd) {
        this.$notify({
          message: this.$t('lang.new_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (this.data.passwd !== this.data.npasswd) {
        this.$notify({
          message: this.$t('lang.notmatchpw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/api/api/forgetpwd', {
        username: this.data.email,
        password: this.data.passwd,
        code: this.data.code,
      })
        .then(() => {
          this.$notify({
            background: '#EAF1FF',
            color: '#3292FF',
            message: this.$t('lang.edit_success'),
          });
        })
        .then(() => {
          this.$router.replace({
            path: this.$router.history.current.query.redirect || '/',
          });
        });
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/login.less';
.btn_content {
  margin-top: 180px;
}
</style>
